// GRID : begin
// main parent container (glass, container)
@mixin glass {
  width: 100%;
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  position: relative; }
// GRID : end

// ICONS
@mixin icon {
  display: block;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: contain; }

// FORM ELEMENTS : begin
// placeholder
@mixin placeholder {
  &::-webkit-input-placeholder {
    @content; }
  &::-moz-placeholder {
    @content; }
  &:-moz-placeholder {
    @content; }
  &:-ms-input-placeholder {
    @content; }
  &:focus::-webkit-input-placeholder {
    color: transparent; }
  &:focus::-moz-placeholder {
    color: transparent; }
  &:focus:-moz-placeholder {
    color: transparent; }
  &:focus:-ms-input-placeholder {
    color: transparent; } }
// text input default
// $fontSize
// $color
// $colorPlaceholder
// $colorFocus
// $colorError
// $border
// $borderRadius
// $padding
// $bg
@mixin default-text-input($fontSize, $color, $colorPlaceholder, $colorFocus, $colorError, $border, $borderRadius, $padding, $bg) {
  cursor: auto;
  width: 100%;
  outline: none;
  border: $border;
  padding: $padding;
  margin: 0;
  border-radius: $borderRadius;
  display: block;
  font-weight: normal;
  font-size: $fontSize;
  line-height: normal;
  font-family: $font-default;
  color: $color;
  text-align: left;
  background: $bg;
  transition: all 0.3s ease;
  @include placeholder {
    color: $colorPlaceholder;
    transition: all 0.3s ease; }
  &:focus {
    outline: none;
    color: $colorFocus;
    border-color: rgba($color-black, 0.5);
    transition: all 0.3s ease; }
  &_error {
    color: $colorError;
    transition: all 0.3s ease; } }
// textarea default
@mixin default-textarea {
  background: $color-white;
  border: 1px solid $color-gray;
  box-sizing: border-box;
  border-radius: 2px;
  font: $font-default;
  font-weight: 400;
  font-size: 13px;
  line-height: normal;
  font-style: normal;
  color: $color-gray-7;
  width: 100%;
  outline: none;
  display: block;
  text-align: left;
  text-transform: none;
  padding: 10px 16px 9px 16px;
  margin: 0;
  resize: vertical;
  -webkit-appearance: none;
  background: transparent;
  transition: color 0.3s ease, borderColor 0.3s ease;
  @include breakpoint($lg) {
    font-weight: 400;
    font-size: 15px; }
  @include placeholder {
    color: $color-gray-7;
    transition: all 0.3s ease; }
  &:focus {
    outline: none;
    color: $color-black-2;
    border-color: $color-blue-21;
    transition: color 0.3s ease, borderColor 0.3s ease; }
  &_error {
     border-color: $color-red-3;
     transition: color 0.3s ease, borderColor 0.3s ease; } }
// button default
@mixin default-btn {
  font-size: 16px;
  font-weight: bold;
  font-family: $font-default;
  line-height: 1;
  background: linear-gradient(180deg, $color-blue-3 0%, $color-blue-4 100%);
  box-shadow: 0px 15px 15px rgba($color-gray-4, 0.7);
  border-radius: 2px;
  color: $color-white;
  user-select: none;
  display: inline-block;
  width: 100%;
  height: auto;
  border: none;
  line-height: 1;
  padding: 17px 0;
  cursor: pointer;
  outline: none;
  text-decoration: none;
  border-radius: 0;
  text-align: center;
  transition: all 0.2s ease;
  // &:hover
  //   background: $color-black
  //   color: $color-white
 }  //   transition: all 0.2s ease
// FORM ELEMENTS : end

// TYPO : begin
// font-face
@function str-replace($string, $search, $replace: "") {
  $index: str-index($string, $search);
  @if $index {
    @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace); }
  @return $string; }

@mixin font-face($name, $path, $weight: null, $style: null, $exts: eot woff2 woff ttf svg) {
  $src: null;
  $extmods: (eot: "?", svg: "#" + str-replace($name, " ", "_"));
  $formats: (otf: "opentype", ttf: "truetype");
  @each $ext in $exts {
    $extmod: if(map-has-key($extmods, $ext), $ext + map-get($extmods, $ext), $ext);
    $format: if(map-has-key($formats, $ext), map-get($formats, $ext), $ext);
    $src: append($src, url(quote($path + "." + $extmod)) format(quote($format)), comma); }
  @font-face {
    font-family: quote($name);
    font-style: $style;
    font-weight: $weight;
    src: $src; } }
