// fluid typography : begin
@function strip-unit($value) {
    @return $value / ($value * 0 + 1); }

@mixin fluid-type($min-vw, $max-vw, $min-font-size, $max-font-size) {
  $u1: unit($min-vw);
  $u2: unit($max-vw);
  $u3: unit($min-font-size);
  $u4: unit($max-font-size);
  @if $u1 == $u2 and $u1 == $u3 and $u1 == $u4 {
    & {
      font-size: $min-font-size;
      @media screen and (min-width: $min-vw) {
        font-size: calc(#{$min-font-size} + #{strip-unit($max-font-size - $min-font-size)} * ((100vw - #{$min-vw}) / #{strip-unit($max-vw - $min-vw)})); }
      @media screen and (min-width: $max-vw) {
        font-size: $max-font-size; } } } }
// example
// with variables
// @include fluid-type($min_width, $max_width, $min_font, $max_font)
// or numbers
// @include fluid-type(768px, 960px, 20px, 30px)

// link-hover
@mixin link-hover($duration, $opacity, $shiftLeft) {
  transition: all $duration ease !important;
  opacity: $opacity !important;
  transform: translate3d($shiftLeft,0,0) !important; }

// titles : begin
@mixin h1 {
  font: bold 18px/1.222 $font-default;
  color: $color-black-2;
  @include fluid-type(320px,1200px,18px,48px);
  @include breakpoint($lg) {
    line-height: 1; } }
@mixin h2 {
  font: bold 18px/1.333 $font-default;
  color: $color-black-2;
  @include fluid-type(320px,1200px,18px,40px);
  @include breakpoint($lg) {
    line-height: 1.2; } }
@mixin h3 {
  font: bold 18px/1.333 $font-default;
  color: $color-black-2;
  @include fluid-type(320px,1200px,18px,32px);
  @include breakpoint($lg) {
    line-height: 1.25; } }
@mixin h4 {
  font: bold 20px/1.4 $font-default;
  color: $color-black-2;
  @include fluid-type(320px,1200px,20px,24px);
  @include breakpoint($lg) {
    line-height: 1.333; } }
@mixin h5 {
  font: 400 13px/1.538 $font-default;
  color: $color-black-2;
  @include fluid-type(320px,1200px,13px,18px);
  @include breakpoint($lg) {
    line-height: 1.333; } }
@mixin h6 {
  font: bold 12px/1.25 $font-default;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: $color-black-2;
  @include breakpoint($lg) {
    line-height: 1.25; } }
// titles : end
// TYPO : end

// POSITION CHILD AT CENTER : begin
@mixin pos-all-center-children {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate3d(-50%, -50%, 0); }

@mixin pos-horizontal-center-children {
  position: relative;
  left: 50%;
  transform: translate3d(-50%, 0, 0); }

@mixin pos-vertical-center-children {
  position: relative;
  top: 50%;
  transform: translate3d(0, -50%, 0); }
// POSITION CHILD AT CENTER : end

// OTHER
@mixin selectionText {
  ::-moz-selection {
    background: $color-blue;
    color: $color-white;
    -webkit-text-fill-color: $color-white;
    -webkit-opacity: 1; }
  ::selection {
    background: $color-blue;
    color: $color-white;
    -webkit-text-fill-color: $color-white;
    -webkit-opacity: 1;
    -webkit-text-fill-color: $color-white;
    -webkit-opacity: 1; } }
.fixed {
  position: fixed;
  top: 0;
  left: 0;
  right: 0; }
.search-autocomplete-default {
  display: none;
  &_active {
    display: block; } }
