.centers {
  padding: 0 0 40px;
  @include breakpoint($lg) {
    padding: 25px 0 40px; }
  .col-lg-7 {
    order: 1;
    @include breakpoint($lg) {
      order: 0; } }
  .col-lg-4 {
    order: 0;
    padding-left: 0;
    padding-right: 0;
    margin: 0 0 20px;
    @include breakpoint($md) {
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($lg) {
      order: 1;
      margin: 0 0 0; } }
  .offset-1 {
    margin-left: 0;
    @include breakpoint($lg) {
      margin-left: 8.33333%; } }
  &__content {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    @include breakpoint($lg) {
      display: block;
      &:after {
        content: '';
        width: 1px;
        height: calc(100% - 78px);
        background: $color-gray;
        position: absolute;
        right: -30px;
        top: 0; } } }
  &__title {
    font: bold 18px/1.222 $font-default;
    color: $color-blue;
    margin: 0 0 10px;
    @include fluid-type(320px,1200px,18px,32px);
    @include breakpoint($lg) {
      font: bold 32px/1.25 $font-default;
      color: $color-blue;
      margin: 0 0 10px; } }
  &__text {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    margin: 0 0 20px;
    @include fluid-type(320px,1200px,13px,15px);
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default;
      color: $color-black-2;
      margin: 0 0 30px; } }
  &__sidebar {
    display: none;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    padding: 0 25px;
    margin: 0 0 20px;
    @include breakpoint($lg) {
      display: flex;
      margin: 0 0 0; }
    &_mobile {
      display: flex;
      justify-content: center;
      visibility: visible;
      opacity: 1;
      @include breakpoint($lg) {
        visibility: hidden;
        opacity: 0; } } }
  .selectric-centers__sidebar_mobile {
    visibility: visible;
    opacity: 1;
    @include breakpoint($lg) {
      visibility: hidden;
      opacity: 0; } }
  &__sidebar-item {
    font: 400 15px/1.6 $font-default;
    color: $color-black-2;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0 0 15px;
    padding: 0 15px!important;
    @include link-hover(0.2s, 1.0, 0);
    &_active {
      @include link-hover(0.2s, 1.0, 0);
      font-weight: bold;
      color: $color-blue-11; }
    &:last-child {
      margin-bottom: 0; }
    &:hover {
      @include link-hover(0.2s, 1.0, 0);
      color: $color-blue-11; }
    &:before {
      content: '';
      margin: 0 10px 0 0;
      position: relative;
      top: 4px;
      .selectric-centers__sidebar & {
        top: 0; } }
    &_recommendations {
      &:before {
        @extend .icon-people-black; }
      &:hover {
        &:before {
          @extend .icon-people-blue-2; } } }
    &_centers {
      &:before {
        @extend .icon-house-black;
        top: 5px; }
      &:hover {
        &:before {
          @extend .icon-house-blue; } } }
    &_certificates {
      &:before {
        @extend .icon-dock-black; }
      &:hover {
        &:before {
          @extend .icon-dock-blue; } } }
    &_statistics {
      &:before {
        @extend .icon-chart-black;
        top: 3.5px; }
      &:hover {
        &:before {
          @extend .icon-chart-blue-2; } } }
    &_search {
      &:before {
        @extend .icon-search-black; }
      &:hover {
        &:before {
          @extend .icon-search-blue; } } }
    &_appeal {
      &:before {
        @extend .icon-letter-black;
        top: 5px; }
      &:hover {
        &:before {
          @extend .icon-letter-blue; } } }
    &_prof {
      &:before {
        top: 5px;
        @extend .icon-certificate-black; }
      &:hover {
        &:before {
          @extend .icon-certificate-blue; } } }
    &_tools {
      &:before {
        top: 5px;
        @extend .icon-list-black; }
      &:hover {
        &:before {
          @extend .icon-list-blue; } } }
    &_cpk {
      &:before {
        @extend .icon-people-plus-black; }
      &:hover {
        &:before {
          @extend .icon-people-plus-blue; } } }
    &_cok {
      &:before {
        top: 5px;
        @extend .icon-house-add-black; }
      &:hover {
        &:before {
          @extend .icon-house-add-blue; } } }
    &_card {
      &:before {
        top: 6px;
        @extend .icon-card-black; }
      &:hover {
        &:before {
          @extend .icon-card-blue-2; } } } }
  .selectric-centers__sidebar {
    .label {
      width: 100%;
      display: flex !important;
      justify-content: flex-start;
      align-items: center;
      padding: 18.5px 10px 18.5px 15px;
      .select-default__icon {
        &-0 {
          @extend .icon-people-black;
          margin: 0 10px 0 0; }
        &-1 {
          @extend .icon-house-black;
          margin: 0 10px 0 0; }
        &-2 {
          @extend .icon-dock-black;
          margin: 0 10px 0 0; }
        &-3 {
          @extend .icon-chart-black;
          margin: 0 10px 0 0; }
        &-4 {
          @extend .icon-search-black;
          margin: 0 10px 0 0; }
        &-5 {
          @extend .icon-letter-black;
          margin: 0 10px 0 0; }
        &-6 {
          @extend .icon-certificate-black;
          margin: 0 10px 0 0; }
        &-7 {
          @extend .icon-list-black;
          margin: 0 10px 0 0; }
        &-8 {
          @extend .icon-people-plus-black;
          margin: 0 10px 0 0; }
        &-9 {
          @extend .icon-house-add-black;
          margin: 0 10px 0 0; } } } }
  &__form {
    background: linear-gradient(180deg, $color-blue-3 0%, $color-blue-4 100%);
    box-shadow: 0px 15px 15px rgba($color-gray-4, 0.7);
    border-radius: 2px;
    padding: 10px;
    margin: 0 0 20px;
    width: 100%;
    @include breakpoint($lg) {
      padding: 20px;
      margin: 0 0 30px; }
    &:last-child {
      margin: 0 0 0; } }
  &__form-row {
    margin: 0 0 10px;
    @include breakpoint($lg) {
      margin: 0 0 15px; }
    &:last-child {
      margin: 0 0 0; } }
  &__search {
    position: relative; }
  &__search-btn {
    position: absolute;
    right: 2px;
    top: calc(50% + 1px);
    transform: translate3d(0,-50%,0);
    background-color: transparent;
    border: none;
    outline: none;
    width: 22px;
    height: 22px;
    z-index: 0;
    cursor: pointer;
    @include breakpoint($lg) {
      right: 16px; }
    &:after {
      content: '';
      @extend .icon-search-red;
      position: absolute;
      left: 6px;
      top: 50%;
      transform: translate3d(-50%,-50%,0);
      width: 11.66px;
      height: 11.66px;
      @include breakpoint($lg) {
        width: 17.5px;
        height: 17.5px; } } }
  &__search-autocomplete {
    background: $color-white;
    box-shadow: 0px 5px 10px rgba($color-blue-14, 0.5);
    list-style: none;
    padding: 7px 0;
    z-index: 3;
    width: 100%;
    position: absolute;
    top: 50px;
    left: 0;
    right: 0; }
  &__search-autocomplete-item {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 15px;
    cursor: pointer;
    margin: 0 0 5px;
    text-decoration: none;
    &:hover {
      background: $color-blue-15;
      cursor: pointer; }
    &:last-child {
      margin: 0 0 0; } }
  &__autocomplete-text {
    text-decoration: none;
    &_bold {
      font: bold 13px/2.066 $font-default;
      color: $color-black-2;
      margin: 0 5px 0 0;
      @include breakpoint($lg) {
        font: bold 15px/2.066 $font-default; } }
    &_regular {
      font: 400 13px/2.066 $font-default;
      color: $color-black-2;
      @include breakpoint($lg) {
        font: 400 15px/2.066 $font-default; } }
    &_number {
      font: 400 13px/1 $font-default;
      color: $color-gray;
      margin: 0 0 0 auto;
      @include breakpoint($lg) {
        font: 400 15px/1 $font-default; } } }
  &__form-select {}
  &__form-option {}
  &__form-input-text {
    @include default-text-input(13px, $color-black-2, $color-gray-7, $color-black-2, $color-gray-6, none, 2px, 11.5px 10px 11.5px 10px, $color-white);
    @include breakpoint($lg) {
      font-size: 15px;
      padding: 16.5px 20px 16.5px 20px; } }
  &__map {
    width: 100%;
    position: relative;
    background: $color-white;
    padding: 0;
    margin: 0 0 10px;
    @include breakpoint($lg) {
      display: block;
      margin: 0 0 30px; } }
  &__map-container {
    width: 100%;
    background: $color-white;
    margin: 0;
    padding: 0;
    display: none;
    @include breakpoint($lg) {
      padding: 20px 20px 0;
      margin: 0 0 20px;
      display: block; }
    &_active {
      display: block; } }
  &__map-link {
    padding: 0 20px 0 20px;
    text-decoration: none;
    background: $color-white;
    box-shadow: 0 0 10px $color-gray-2;
    font: 400 13px/1 $font-default;
    color: $color-blue-5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 48px;
    width: 100%;
    @include breakpoint($lg) {
      font: 400 15px/1 $font-default;
      justify-content: center; }
    &:after {
      content: '';
      margin: 0 0 0 23px;
      @extend .icon-arrow-red-bottom;
      width: 12px;
      height: 7.41px; }
    .centers__map-container_active + & {
      &:after {
        @extend .icon-arrow-red-top; }
      @include breakpoint($lg) {
        &:after {
          width: 12px;
          height: 7.41px; } } } }
  iframe {
    border: none;
    outline: none;
    width: 100%; }
  &__list {
    margin: 0 0 20px;
    @include breakpoint($lg) {
      margin: 0 0 30px; } }
  &__list-item {
    padding: 43px 0 0 0;
    background: $color-white;
    border-radius: 2px;
    box-shadow: 0px 0px 10px $color-blue-14;
    margin: 0 0 10px;
    position: relative;
    @include breakpoint($lg) {
      padding: 26px 0 0 0;
      margin: 0 0 20px; }
    &:last-child {
      margin: 0 0 0; } }
  &__list-item-title {
    font: bold 15px/1.333 $font-default;
    color: $color-black-2;
    margin: 0 0 10px;
    padding: 0 15px;
    @include breakpoint($lg) {
      font: bold 16px/1.5 $font-default;
      padding: 0 30px; } }
  &__list-item-descr {
    font: 400 13px/1.6 $font-default;
    color: $color-black-2;
    margin: 0 0 22px;
    padding: 0 15px;
    @include breakpoint($lg) {
      font: 400 15px/1.538 $font-default;
      padding: 0 30px;
      margin: 0 0 14px; } }
  &__list-item-contacts {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding: 0 15px;
    margin: 0 0 20px;
    flex-direction: column;
    @include breakpoint($lg) {
      padding: 0 30px;
      flex-direction: row;
      margin: 0 0 15px; } }
  &__list-item-contacts-item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font: bold 13px/1.538 $font-default;
    color: $color-blue-11;
    text-decoration: none;
    margin: 0 0 8px;
    position: relative;
    @include link-hover(0.2s,1,0);
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default;
      margin: 0 22px 0 0; }
    &:hover {
      @include link-hover(0.2s,0.8,0); }
    &:last-child {
      margin: 0 0 0 0; }
    &:before {
      content: '';
      margin: 0 7px 0 0; }
    &_phone {
      &:before {
        @extend .icon-phone;
        width: 16px;
        height: 16px; } }
    &_letter {
      &:before {
        @extend .icon-mail-blue;
        width: 14px;
        height: 11.206px; } }
    &_link {
      &:before {
        @extend .icon-launch; } } }
  &__list-item-text {
    padding: 0 15px;
    margin: 0 0 22px;
    @include breakpoint($lg) {
      padding: 0 30px; } }
  &__list-item-text-content {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    margin: 0 0 1px;
    display: flex; }
  &__list-item-text-link {
    font: 400 13px/1.538 $font-default;
    color: $color-blue-11;
    text-decoration: none; }
  &__list-item-link {
    padding: 0 30px;
    font: bold 12px/1.25 $font-default;
    color: $color-blue-11;
    padding: 0 30px;
    height: 56px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-transform: uppercase;
    letter-spacing: 1px;
    text-decoration: none;
    border-top: 1px solid $color-blue-15;
    position: relative;
    @include link-hover(0.2s,1,0);
    &:hover {
      @include link-hover(0.2s,0.8,0); }
    &:after {
      content: '';
      @extend .icon-arrow-blue-right-2;
      width: 7.41px;
      height: 12px; } }
  &__list-number {
    font: bold 13px/1.538 $font-default;
    color: $color-red;
    position: absolute;
    left: 15px;
    top: 13px;
    @include breakpoint($lg) {
      left: auto;
      right: 30px;
      top: 27px; } } }
