.statistics {
  padding: 26px 0 24px;
  background: $color-white;
  box-shadow: 0px 0px 30px rgba($color-blue-9, 0.8);
  overflow-x: scroll;
  @include breakpoint($lg) {
    overflow: auto; }
  .row {
    flex-wrap: nowrap;
    @include breakpoint($lg) {
      flex-wrap: wrap; } }
  .slick-slide {
    width: 150px;
    @include breakpoint($lg) {
      width: auto; } }
  .col-2 {
    flex: 0 0 150px;
    max-width: 150px;
    border: none;
    outline: none;
    @include breakpoint($lg) {
      flex: 0 0 16.66667%;
      max-width: 16.66667%; } }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  &__item-number {
    font: bold 18px/1.222 $font-default;
    padding: 0 0 6px;
    margin: 0 0 5px;
    color: $color-blue-6;
    border-bottom: 2px $color-red solid;
    width: 100%;
    text-align: center;
    @include breakpoint($lg) {
      font: bold 48px/1 $font-default;
      padding: 0 0 4px;
      margin: 0 0 10px; } }
  &__item-descr {
    font: 400 13px/1.538 $font-default;
    color: $color-blue-6;
    text-align: center;
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default; } } }
