.cover {
  padding: 35px 0 36px 0;
  background-size: cover;
  background-image: url('../img/bgs/bg-cover.png');
  background-repeat: no-repeat;
  background-position: center center;
  @include breakpoint($lg) {
    padding: 78px 105px 79px 105px; }
  .container {}
  &__title {
    font: bold 18px/1.222 $font-default;
    color: $color-white;
    margin: 0 0 15px;
    @include breakpoint($lg) {
      font: bold 32px/1.25 $font-default;
      margin: 0 0 5px; } }
  &__text {
    font: 400 13px/1.538 $font-default;
    color: $color-white;
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default; } } }
