.conclusion {
  padding: 18px 0;
  overflow: hidden;
  @include breakpoint($lg) {
    padding: 27px 0; }
  .container {
    padding-left: 0;
    padding-right: 0;
    @include breakpoint($lg) {
      padding-left: 15px;
      padding-right: 15px; } }
  .offset-1 {
    margin-left: 0;
    @include breakpoint($lg) {
      margin-left: 8.33333%; } }
  &__title {
    @include h3;
    color: $color-blue;
    margin: 0 0 21px;
    padding: 0 15px;
    @include breakpoint($lg) {
      margin: 0 0 30px;
      padding: 0 0; } }
  &__content {
    background: $color-white;
    box-shadow: 0 0 10px $color-blue-14;
    border-radius: 0 0 2px 2px;
    padding: 17px 0 22px;
    position: relative;
    @include breakpoint($lg) {
      padding: 28px 0 30px; } }
  &__content-label {
    font: bold 13px/1.538 $font-default;
    color: $color-red;
    padding: 0 15px;
    margin: 0 0 10px;
    @include breakpoint($lg) {
      padding: 0 30px; } }
  &__content-subtitle {
    padding: 0 15px;
    font: bold 20px/1.222 $font-default;
    color: $color-black-2;
    margin: 0 0 12px;
    @include fluid-type(320px,1200px,18px,24px);
    @include breakpoint($lg) {
      line-height: 1.333;
      margin: 0 0 20px;
      padding: 0 30px; }
    &_small {
      font: 400 15px/1.466 $font-default;
      color: $color-black-2;
      padding: 0 0 0 0;
      margin: 0 0 11px;
      @include fluid-type(320px,1200px,15px,18px);
      @include breakpoint($lg) {
        line-height: 1.333;
        margin: 0 0 15px; } } }
  &__btn {
    position: relative;
    right: auto;
    top: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $color-white;
    padding: 18.5px 15px;
    border-radius: 4px;
    background: $color-blue-11;
    text-decoration: none;
    font-size: 0;
    line-height: 0;
    margin: 21px 0 0 0;
    &:after {
      content: 'Скачать DOCX';
      display: block;
      font: 400 12px/1 $font-default;
      color: $color-white; }
    &:before {
      content: '';
      @extend .icon-msword-white;
      margin: 0 5px 0 0; }
    @include breakpoint($lg) {
      justify-content: flex-start;
      font: 400 13px/1 $font-default;
      position: absolute;
      right: 30px;
      top: 22px;
      padding: 9.5px 15px;
      margin: 0 0 0 0;
      &:after {
        display: none; } } }
  &__content-row {
    padding: 0 15px 20px 15px;
    margin: 0 0 20px;
    border-bottom: 1px solid $color-blue-15;
    @include breakpoint($lg) {
      padding: 0 30px 30px 30px; }
    &:last-child {
      border-bottom: none;
      padding-bottom: 0;
      margin-bottom: 0; } }
  &__content-item {
    margin: 0 0 10px;
    &_indent-big {
      margin: 0 0 15px; }
    &:last-child {
      margin-bottom: 0; } }
  &__content-text {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    margin: 0 0 6px;
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default;
      margin: 0 0 0; }
    &:last-child {
      margin-bottom: 0; } }
  &__content-link {
    color: $color-blue-11;
    text-decoration: none; } }
