.pagination {
  width: 100%;
  &__container {
    display: flex;
    justify-content: center;
    align-items: center;
    @include breakpoint($lg) {
      justify-content: flex-start; } }
  &__col {
      display: flex;
      justify-content: flex-start;
      align-items: center; }
  &__form {
    padding: 12px 15px;
    background: $color-white;
    box-shadow: 0px 0px 10px $color-gray-2;
    border-radius: 4px;
    margin: 0 20px 0 0;
    display: flex;
    justify-content: flex-start;
    align-items: center; }
  &__input-text {
    @include default-text-input(15px, $color-gray-9, $color-gray-9, $color-gray-9, $color-gray-9, 1px solid $color-gray, 4px, 2.5px 6.9px, $color-white);
    margin: 0 10px 0 0; }
  &__text {
    font: 400 15px/1.066666666666667 $font-default;
    color: $color-black-2;
    margin: 0 10px 0 0; }
  &__link {
    font: 400 15px/1.466 $font-default;
    color: $color-blue-5;
    text-decoration: none; }
  &__btn {
    box-shadow: 0 0 10px $color-blue-14;
    background: $color-white;
    border-radius: 3px;
    font: 400 15px/1.6 $font-default;
    color: $color-blue-5;
    border-radius: 3px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 12px 28px 12px;
    text-decoration: none;
    position: relative;
    text-indent: -9999px;
    @include link-hover(0.2s,1,0);
    @include breakpoint($lg) {
      text-indent: 0;
      min-width: auto;
      padding: 12px 39px 12px 24px; }
    &:hover {
      @include link-hover(0.2s,1.0,0);
      background: $color-blue-20; }
    &:after {
      content: '';
      @extend .icon-arrow-blue-right-2;
      width: 9px;
      height: 15.428px;
      margin: 0 0 0 0;
      position: absolute;
      right: auto;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%, -50%, 0); }
    @include breakpoint($lg) {
      &:after {
        right: 18px;
        left: auto;
        top: 50%;
        transform: translate3d(0, -50%, 0); } }
    &_back {
      margin-right: 20px;
      padding: 12px 28px 12px;
      &:after {
        // @extend .icon-arrow-blue-left
        @include icon;
        background-image: url('../img/icons/icon-arrow-blue-left-2.svg');
        width: 9px;
        height: 15.428px; }
      @include breakpoint($lg) {
        padding: 12px 24px 12px 39px;
        &:after {
          right: auto;
          left: 18px;
          top: 50%;
          transform: translate3d(0, -50%, 0); } } }
    &_disable {
      background: $color-blue-15;
      color: $color-gray-6;
      display: flex;
      @include breakpoint($lg) {
        display: none; }
      &:hover {
        background: $color-blue-15;
        color: $color-gray-6; }
      &:after {
        @extend .icon-arrow-gray-right; }
      &.pagination__btn_back {
        &:after {
          @extend .icon-arrow-gray-left; } } } } }
