.breadcrumbs {
  padding: 0 15px;
  @include breakpoint($lg) {
    padding: 0; }
  &__list {
    font: 400 13px/1.538 $font-default;
    color: $color-gray-7;
    margin: 0 0 12px;
    @include breakpoint($lg) {
      margin: 0 0 17px; } }
  &__item {
    text-decoration: none;
    font: 400 13px/1.538 $font-default;
    color: $color-gray-7; } }
