.other-sites {
  width: 100%;
  max-width: 1200px;
  position: absolute;
  top: 40px;
  left: 50%;
  transform: translate3d(-50%,0,0);
  opacity: 0;
  visibility: hidden;
  z-index: 9999;
  display: flex;
  flex-wrap: wrap;
  align-items: stretch;
  // background: $color-blue-6
  &_active {
    visibility: visible;
    opacity: 1; }
  .row.no-gutters {
    margin-right: 0;
    margin-left: 0;
    &>[class^="col-"],
    &>[class*=" col-"] {
      padding-right: 0;
      padding-left: 0; } }
  .col-6 {
    &:first-child {
      background: $color-blue-6; }
    &:last-child {
      background: $color-white; } }
  &__tabs {
    padding: 26px 0 26px 0; }
  &__tabs-item {
    cursor: pointer;
    padding: 15px 56px 15px 52px;
    position: relative;
    background: transparent;
    &:last-child {
      &:after {
        display: none; } }
    &:before {
      content: '';
      @extend .icon-arrow-white-right-2;
      position: absolute;
      right: 64px;
      top: 50%;
      transform: translate3d(0,-50%,0);
      opacity: 0.2;
      transition: all 0.3s ease; }
    &:after {
      content: '';
      display: block;
      width: calc(100% - 108px);
      height: 1px;
      position: absolute;
      left: 52px;
      bottom: -1px;
      background: rgba($color-blue-12, 0.2);
      transition: all 0.3s ease; }
    &_active,
    &:hover {
      background: $color-blue-13;
      &:before {
        opacity: 1; }
      &:after {
        display: none; } } }
  &__tabs-item-title {
    font: 400 15px/1.6 $font-default;
    color: $color-white;
    margin: 0 0 5px; }
  &__tabs-item-descr {
    font: 400 13px/1.538 $font-default;
    color: $color-gray-6; }
  &__content {}
  &__content-item {
    display: none;
    padding: 45px 20px 61px 30px;
    &_active {
      display: block; } }
  &__content-item-title {
    font: bold 24px/1.333 $font-default;
    color: $color-blue-6;
    margin: 0 0 12px; }
  &__content-item-text {
    font: 400 15px/1.6 $font-default;
    color: $color-gray-6;
    margin: 0 0 18px; }
  &__descr {
    width: 100%;
    max-width: 586px; }
  &__title {
    font: bold 18px/1.333 $font-default;
    color: $color-blue-6;
    margin: 0 0 11px; }
  &__item {
    &:nth-child(2) {
      margin: 0 0 6px; }
    &:nth-child(3) {
      margin: 0 0 6px; }
    &:nth-child(4) {
      margin: 0 0 6px; } }
  &__subtitle {
    font: 400 13px/1.538 $font-default;
    color: $color-gray-6;
    .other-sites__item:nth-child(2) & {
      margin: 0 0 5px; } }
  &__link {
    font: 400 15px/1.6 $font-default;
    color: $color-blue-11;
    text-decoration: none;
    position: relative;
    padding: 0 0 0 21px;
    @include link-hover(0.2s, 1, 0);
    &:hover {
      @include link-hover(0.2s, 0.8, 0); }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0; }
    &_address {
      align-items: flex-start;
      &:before {
        @extend .icon-marker;
        top: 4px; } }
    &_email {
      &:before {
        @extend .icon-mail-blue-2;
        top: 4px; } }
    &_phone {
      &:before {
        @extend .icon-phone;
        top: 1.5px; } } }
  &__text-link {
    font: 400 13px/1.538 $font-default;
    color: $color-gray-6; } }
