*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }
html {
  height: 100%;
  min-height: 100%;
  position: relative;
  margin: 0;
  padding: 0; }
body,
.body {
  margin: 0;
  padding: 0;
  position: relative;
  width: 100%;
  // height: 100%
  background-color: $color-white;
  &_hidden {
    overflow: hidden; }
  &_bg-gray {
    background: $color-gray-3; } }
