.nav-mobile {
  display: none;
  width: 100%;
  position: absolute;
  left: 0;
  right: 0;
  top: 60px;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  z-index: 9999;
  &_active {
    display: flex; }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    &_other-sites {
      display: none; }
    &_primary {}
    &_hidden {
      display: none; }
    &_active {
      display: flex; } }
  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    border-bottom: 5px solid $color-blue-16; }
  &__link {
    font: 400 13px/60px $font-default;
    color: $color-white;
    height: 60px;
    text-decoration: none;
    padding: 0 15px;
    background: $color-blue;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    &_active,
    &:hover {
      background: $color-blue-11; }
    &:after {
      content: ''; }
    &_enter {
      &:after {
        @extend .icon-social-blue-2; } }
    &_search {
      &:after {
        @extend .icon-arrow-blue-right-3; } }
    &_back {
      justify-content: flex-start;
      &:before {
        content: '';
        @extend .icon-arrow-blue-left;
        margin: 0 13px 0 0; } } }
  &__footer {
    width: 100%;
    background: $color-blue-17;
    padding: 0 15px;
    height: 60px; }
  &__footer-list {
    display: flex;
    justify-content: flex-start;
    align-items: center; }
  &__footer-item {
    font: 400 13px/60px $font-default;
    color: $color-white;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    &:before {
      content: '';
      margin: 0 7.5px 0 0; }
    &_version {
      &:before {
        @extend .icon-help; } }
    &_help {
      padding: 0 0 0 13px;
      margin: 0 0 0 13px;
      &:after {
        content: '';
        width: 1px;
        height: 40px;
        background: rgba($color-white,0.2);
        opacity: 0.5;
        position: absolute;
        left: 0;
        top: 50%;
        transform: translate3d(0,-50%,0); }
      &:before {
        @extend .icon-help; } } }
  &__list-item {
    cursor: pointer;
    width: 100%; }
  &__list-item-title {
    font: 400 13px/60px $font-default;
    color: $color-white;
    height: 60px;
    text-decoration: none;
    padding: 0 15px;
    background: $color-blue;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: relative;
    &:after {
      content: '';
      @extend .icon-arrow-blue-bottom-2; }
    .nav-mobile__list-item_active & {
      &:after {
        @extend .icon-arrow-blue-top; } } }
  &__list-item-content {
    background: $color-white;
    padding: 18px 15px 24px;
    display: none;
    &_active {
      display: block; } }
  &__content-item-title {
    font: bold 16px/1.5 $font-default;
    color: $color-blue-6;
    margin: 0 0 9px; }
  &__content-item-text {
    font: 400 13px/1.538 $font-default;
    color: $color-gray-6;
    margin: 0 0 22px; }
  &__descr {
    width: 100%; }
  &__line-title {
    font: 400 13px/1.538461538461538 $font-default;
    color: $color-blue-6;
    margin: 0 0 12px; }
  &__line-item {
    margin: 0 0 10px; }
  &__subtitle {
    font: 400 12px/1.333 $font-default;
    color: $color-gray-6;
    margin: 0 0 5px; }
  &__line-link {
    font: 400 12px/1.333 $font-default;
    color: $color-blue-11;
    text-decoration: none;
    position: relative;
    &_address {
      align-items: flex-start; }
    &_email {}
    &_phone {} }
  &__text-link {
    font: 400 12px/1.333 $font-default;
    color: $color-gray-6; } }
