.send-appellation {
  padding: 0 0 15px;
  @include breakpoint($lg) {
    padding: 25px 0 40px; }
  .col-lg-7 {
    order: 1;
    @include breakpoint($lg) {
      order: 0; } }
  .col-lg-4 {
    order: 0;
    padding-left: 0;
    padding-right: 0;
    margin: 0 0 20px;
    @include breakpoint($md) {
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($lg) {
      order: 1;
      margin: 0 0 0; } }
  .offset-1 {
    margin-left: 0;
    @include breakpoint($lg) {
      margin-left: 8.33333%; } }
  &__content {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    @include breakpoint($lg) {
      display: block;
      &:after {
        content: '';
        width: 1px;
        // height: calc(100% - 78px)
        height: 100%;
        background: $color-gray;
        position: absolute;
        right: -30px;
        top: 0; } } }
  &__title {
    @include h1;
    color: $color-blue;
    margin: 0 0 10px; }
  &__descr {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    margin: 0 0 12px;
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default;
      margin: 0 0 30px; } }
  &__form-search {
    padding: 10px;
    background: linear-gradient(180deg, $color-blue-3 0%, $color-blue-4 100%);
    border-radius: 2px;
    margin: 0 0 4px;
    box-shadow: 0px 15px 15px rgba($color-gray-4, 0.7);
    position: relative;
    z-index: 1;
    @include breakpoint($lg) {
      padding: 20px;
      margin: 0 0 30px;
      box-shadow: none; } }
  &__search {
    position: relative; }
  &__search-btn {
    position: absolute;
    right: 2px;
    top: calc(50% + 1px);
    transform: translate3d(0,-50%,0);
    background-color: transparent;
    border: none;
    outline: none;
    width: 22px;
    height: 22px;
    z-index: 0;
    cursor: pointer;
    @include breakpoint($lg) {
      right: 16px; }
    &:after {
      content: '';
      @extend .icon-search-red;
      position: absolute;
      left: 6px;
      top: 50%;
      transform: translate3d(-50%,-50%,0);
      width: 11.66px;
      height: 11.66px;
      @include breakpoint($lg) {
        width: 17.5px;
        height: 17.5px; } } }
  &__search-autocomplete {
    background: $color-white;
    box-shadow: 0px 5px 10px rgba($color-blue-14, 0.5);
    list-style: none;
    padding: 7px 0;
    z-index: 3;
    width: 100%;
    position: absolute;
    top: 50px;
    left: 0;
    right: 0; }
  &__search-autocomplete-item {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 15px;
    cursor: pointer;
    margin: 0 0 5px;
    text-decoration: none;
    &:hover {
      background: $color-blue-15;
      cursor: pointer; }
    &:last-child {
      margin: 0 0 0; } }
  &__autocomplete-text {
    text-decoration: none;
    &_bold {
      font: bold 13px/2.066 $font-default;
      color: $color-black-2;
      margin: 0 5px 0 0;
      @include breakpoint($lg) {
        font: bold 15px/2.066 $font-default; } }
    &_regular {
      font: 400 13px/2.066 $font-default;
      color: $color-black-2;
      @include breakpoint($lg) {
        font: 400 15px/2.066 $font-default; } }
    &_number {
      font: 400 13px/1 $font-default;
      color: $color-gray;
      margin: 0 0 0 auto;
      @include breakpoint($lg) {
        font: 400 15px/1 $font-default; } } }
  &__form-select {}
  &__form-option {}
  &__form-input-text {
    @include default-text-input(13px, $color-black-2, $color-gray-7, $color-black-2, $color-gray-6, 1px solid $color-gray, 2px, 11.5px 10px 11.5px 10px, $color-white);
    @include breakpoint($lg) {
      font-size: 15px;
      padding: 16.5px 20px 16.5px 20px; }
    &_search {
      .send-appellation__form-search & {
        @include default-text-input(13px, $color-black-2, $color-gray-7, $color-black-2, $color-gray-6, none, 2px, 11.5px 10px 11.5px 10px, $color-white);
        @include breakpoint($lg) {
          font-size: 15px;
          padding: 16.5px 20px 16.5px 20px; } } } }
  &__form-card {
    margin: 0 0 0;
    padding: 29px 10px 20px;
    background: $color-white;
    box-shadow: 0 15px 15px rgba($color-gray-4, 0.7);
    border-radius: 2px;
    @include breakpoint($lg) {
      padding: 30px 20px 30px 20px;
      margin: 0 0 17px; }
    .selectric {
      border: 1px solid $color-gray;
      margin: 0 0 10px; } }
  &__form-row {
    margin: 0 0 20px;
    @include breakpoint(lg) {
      margin: 0 0 30px; }
    &_two-col {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: column;
      @include breakpoint($lg) {
        flex-direction: row;
        align-items: center; } }
    &_indent-20 {
      margin: 0 0 37px;
      @include breakpoint($lg) {
        margin: 0 0 20px; } }
    &:last-child {
      margin-bottom: 0; } }
  &__form-card-title {
    font: bold 13px/1.538 $font-default;
    color: $color-blue;
    margin: 0 0 13px;
    @include breakpoint($lg) {
      margin: 0 0 10px; } }
  .selectric-centers__sidebar_mobile {
    visibility: visible;
    opacity: 1;
    @include breakpoint($lg) {
      visibility: hidden;
      opacity: 0; } }
  &__input-textarea {
    @include default-textarea;
    height: 80px;
    padding: 11.5px 10px 11.5px 10px;
    @include breakpoint($lg) {
      height: 100px; } }
  &__file-wrapper {
    margin: 10px 0 0 0; }
  &__file-dropzone {
    width: 100%;
    height: 75px;
    display: none;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: $color-white;
    border: 1px dashed $color-gray;
    border-radius: 2px;
    cursor: pointer;
    margin: 0 0 19px;
    @include breakpoint($lg) {
      display: flex; } }
  &__file-dropzone-title {
    font: normal 15px/1.6 $font-default;
    margin: 0 0 3px;
    color: $color-gray-7; }
  &__file-dropzone-descr {
    font: 400 13px/1.538 $font-default;
    color: $color-gray-7; }
  &__file-list {
    display: none;
    @include breakpoint($lg) {
      display: block; } }
  &__file-list-item {
    position: relative;
    margin: 0 0 18px;
    &:last-child {
      margin: 0 0 0; } }
  &__file-list-item-text {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    margin: 0 0 6px; }
  &__file-list-item-line {
    width: 100%;
    border-bottom: 1px solid $color-blue-15;
    .send-appellation__file-list-item_process & {
      background: $color-blue-15;
      height: 2px;
      border-bottom: none;
      position: relative;
      &:after {
        content: '';
        display: block;
        background: $color-blue-3;
        width: 100px;
        height: 2px; } }
    .send-appellation__file-list-item_error & {
      background: $color-red-3;
      height: 2px;
      border-bottom: none; }
    .send-appellation__file-list-item_success & {
      background: transparent;
      height: 2px;
      background: $color-blue-15;
      border-bottom: none; } }
  &__file-list-item-close {
    @extend .icon-close-gray;
    position: absolute;
    right: 5px;
    top: 3px;
    cursor: pointer;
    @include link-hover(0.2s, 1.0, 0);
    &:hover {
      @include link-hover(0.2s, 0.8, 0); } }
  &__file-list-item-reload {
    @extend .icon-reload-gray;
    position: absolute;
    right: 28px;
    top: 2px;
    cursor: pointer;
    @include link-hover(0.2s, 1.0, 0);
    &:hover {
      @include link-hover(0.2s, 0.8, 0); } }
  &__btn-mobile {
    font: 400 13px/1.538 $font-default;
    color: $color-blue-3;
    border: 1px solid $color-gray;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    justify-content: center;
    padding: 9.5px 0;
    @include link-hover(0.2s, 1.0, 0);
    @include breakpoint($lg) {
      display: none; }
    &:hover,
    &_pressed {
      color: $color-blue-22;
      @include link-hover(0.2s, 0.8, 0); } }
  &__how-contact {}
  &__how-contact-title {
    font: bold 15px/1.6 $font-default;
    color: $color-black-2;
    margin: 0 0 10px; }
  &__radio-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    margin: 0 0 13px;
    flex-direction: column;
    @include breakpoint($lg) {
      flex-direction: row;
      align-items: center; } }
  &__radio-item {
    margin: 0 27px 8px 0;
    position: relative;
    @include breakpoint($lg) {
      margin: 0 27px 0 0; }
    &:last-child {
      margin: 0 0 0 0; } }
  &__radio-input {
    &:checked,
    &:not(:checked) {
      position: absolute;
      left: -9999px; }
    &:checked + label,
    &:not(:checked) + label {
      position: relative;
      padding-left: 24px;
      cursor: pointer;
      line-height: 24px;
      display: inline-block;
      color: $color-black-2; }
    &:checked + label:before,
    &:not(:checked) + label:before {
      content: '';
      position: absolute;
      left: 0;
      top: 50%;
      width: 16px;
      height: 16px;
      border: 1px solid $color-gray;
      border-radius: 8px;
      background: $color-white;
      transform: translate3d(0,-50%,0); }
    &:checked + label:after,
    &:not(:checked) + label:after {
      content: '';
      width: 16px;
      height: 16px;
      background: $color-white;
      border: 5px solid $color-blue-3;
      position: absolute;
      top: 50%;
      left: 0;
      transform: translate3d(0,-50%,0);
      border-radius: 100%;
      transition: all 0.2s ease; }
    &:not(:checked) + label:after {
      opacity: 0;
      transform: scale(0), translate3d(0,-50%,0); }
    &:checked + label:after {
      opacity: 1;
      transform: scale(1), translate3d(0,-50%,0); } }
  &__btn {
    @include default-btn;
    width: 100%;
    margin: 0 0 20px 0;
    @include breakpoint($lg) {
      width: 240px;
      margin: 0 40px 0 0; } }
  &__personal-data-text {
    font: 400 12px/1.666 $font-default;
    color: $color-gray-7;
    @include breakpoint($lg) {
      font: 400 13px/1.538 $font-default; } }
  &__personal-data-link {
    text-decoration: none;
    color: $color-blue-11; }
  .selectric-centers__sidebar {
    .label {
      width: 100%;
      display: flex !important;
      justify-content: flex-start;
      align-items: center;
      padding: 18.5px 10px 18.5px 15px;
      .select-default__icon {
        &-0 {
          @extend .icon-people-black;
          margin: 0 10px 0 0; }
        &-1 {
          @extend .icon-house-black;
          margin: 0 10px 0 0; }
        &-2 {
          @extend .icon-dock-black;
          margin: 0 10px 0 0; }
        &-3 {
          @extend .icon-chart-black;
          margin: 0 10px 0 0; }
        &-4 {
          @extend .icon-search-black;
          margin: 0 10px 0 0; }
        &-5 {
          @extend .icon-letter-black;
          margin: 0 10px 0 0; }
        &-6 {
          @extend .icon-certificate-black;
          margin: 0 10px 0 0; }
        &-7 {
          @extend .icon-list-black;
          margin: 0 10px 0 0; }
        &-8 {
          @extend .icon-people-plus-black;
          margin: 0 10px 0 0; }
        &-9 {
          @extend .icon-house-add-black;
          margin: 0 10px 0 0; } } } } }
