.header {
  background: $color-white;
  z-index: 4;
  display: none;
  @include breakpoint($lg) {
    display: block; }
  &__row {
    // overflow: hidden
    &_top {
      height: 40px;
      background: $color-blue;
      display: flex;
      align-items: center;
      .container {
        display: flex;
        justify-content: flex-start;
        align-items: center; } }
    &_bottom {
      height: 60px;
      background: $color-white;
      display: flex;
      align-items: center;
      .container {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        flex-direction: row; } }
    &_links {
      background: $color-blue-18;
      .container {
        position: relative;
        display: flex;
        justify-content: flex-start;
        align-items: center;
        height: 40px !important;
        border-top: 1px solid $color-gray;
        &:before {
          content: '';
          width: 50%;
          height: 1px;
          background: $color-gray;
          position: absolute;
          left: 0;
          top: -1px;
          transform: translate3d(-100%,0,0); }
        &:after {
          content: '';
          display: none;
          width: 50%;
          height: 1px;
          background: $color-gray;
          position: absolute;
          right: 0;
          top: -1px;
          transform: translate3d(100%,0,0); }
        @include breakpoint($md) {
          &:before,
          &:after {
            display: block; } }
        @include breakpoint($lg) {
          border-top: none;
          &:before,
          &:after {
            display: none; } } } } }
  &__select {}
  &__select-title {
    font: 400 13px/1.538 $font-default;
    color: $color-white;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 7px 0 8px;
    cursor: pointer;
    &:before {
      content: '';
      @extend .icon-squares;
      margin: 0 7px 0 0; }
    &:after {
      content: '';
      @extend .icon-arrow-white-bottom;
      margin: 0 0 0 8px; }
    &_active {
      &:after {
        @extend .icon-arrow-white-top; } }
    @include link-hover(0.2s,1,0);
    &:hover {
      @include link-hover(0.2s,0.8,0); } }
  &__links {
    margin: 0 0 0 auto;
    display: flex;
    align-items: center;
    justify-content: flex-start; }
  &__link {
    font: 400 13px/1.538 $font-default;
    color: $color-white;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include link-hover(0.2s,1,0);
    &:hover {
      @include link-hover(0.2s,0.8,0); }
    &_version {
      &:before {
        content: '';
        @extend .icon-eye;
        margin: 0 6px 0 0; }
      &:after {
        content: '';
        width: 1px;
        height: 20px;
        background: rgba($color-white, 0.2);
        opacity: 0.5;
        margin: 0 17px 0 17px; } }
    &_help {
      &:before {
        content: '';
        @extend .icon-help;
        margin: 0 6px 0 0; } }
    &_search {
      font: 400 15px/1.6 $font-default;
      color: $color-blue-2;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-search;
        margin: 0.5px 8px 0 0; }
      &:after {
        content: '';
        display: block;
        width: 1px;
        height: 40px;
        background: rgba($color-gray, 0.5);
        margin: 0 20px 0 20px; } }
    &_office {
      font: 400 15px/1.6 $font-default;
      color: $color-blue-2;
      &:before {
        content: '';
        @extend .icon-social-blue;
        margin: 0 8px 0 0; } }
    &_enter {
      @extend .header__link_office;
      &:after {
        display: none; } }
    &_exit {
      @extend .icon-enter;
      margin: 0 0 0 12px; }
    &_hidden {
      display: none; } }
  &__logo {
    @extend .icon-logo;
    margin: 0 20px 0 0; }
  &__nav {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 auto 0 0;
    &_hidden {
      display: none; }
    &:before {
      content: '';
      display: block;
      width: 1px;
      height: 40px;
      background: rgba($color-gray, 0.5);
      margin: 0 20px 0 0; } }
  &__nav-item {
    font: 400 15px/1.6 $font-default;
    color: $color-blue-2;
    text-decoration: none;
    margin: 0 30px 0 0;
    @include link-hover(0.2s,1,0);
    &:hover,
    &_active {
      @include link-hover(0.2s,0.8,0);
      color: $color-red; }
    &:last-child {
      margin: 0 0 0 0; } }
  &__search-wrapper {
    &_active {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      flex-grow: 1;
      margin: 0 20px 0 0;
      &:before {
        content: '';
        display: block;
        width: 1px;
        height: 40px;
        background: rgba($color-gray, 0.5); }
      &:after {
        content: '';
        display: block;
        width: 1px;
        height: 40px;
        background: rgba($color-gray, 0.5); } } }
  &__list {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 0 0 0 -8px;
    scrollbar-width: none;
    overflow-x: scroll;
    &::-webkit-scrollbar {
      display: none; }
    // padding: 5px 0 5px 0
    @include breakpoint($lg) {
      overflow: auto;
      margin: 0 0 0 0;
 } }      // padding: 0 0 0 0
  &__list-link {
    font: 400 13px/20px $font-default;
    color: $color-white;
    text-decoration: none;
    padding: 9.5px 8px 9.5px 8px;
    background: transparent;
    outline: none;
    user-select: none;
    border: none;
    white-space: nowrap;
    @include breakpoint($lg) {
      font: 400 13px/40px $font-default;
      padding: 0 6px;
      width: auto;
      white-space: nowrap; }
    &:hover {
      @include breakpoint($lg) {
        background: $color-blue-19; } } } }
