.services {
  background: $color-gray-3;
  padding: 15px 0 15px;
  position: relative;
  z-index: 1;
  @include breakpoint($lg) {
    padding: 70px 0 40px; }
  ._hidden {
    display: none;
    visibility: hidden; }
  &__item-wrapper {}
  &__item {
    background: $color-white-2;
    height: 108px;
    margin: 0 0 10px;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 20px;
    border-radius: 2px;
    box-shadow: 0px 0px 10px $color-gray-2;
    position: relative;
    transition: all 0.5s ease, background 0.8s linear;
    cursor: pointer;
    transform: scale(1.0);
    @include breakpoint($lg) {
      display: block; }
    @include breakpoint($lg) {
      margin: 0 0 30px; }
    &:before {
      position: absolute;
      top: 50%;
      transform: translate3d(0,-50%,0);
      @include breakpoint($lg) {
        top: auto;
        transform: translate3d(0,0,0); } }
    .services__item-wrapper:hover & {
      @include breakpoint($lg) {
        transform: translate3d(0,-10px,0);
        box-shadow: 0px 15px 15px rgba($color-gray-4, 0.7);
        background: linear-gradient(180deg, $color-blue-3 0%, #0845B2 100%); } }
    .services__item-wrapper:active & {
      transform: scale(0.99);
      @include breakpoint($lg) {
        transform: scale(1.0); } }
    &_recommendations {
      &:before {
        content: '';
        @extend .icon-people-blue;
        right: 25px;
        @include breakpoint($lg) {
          top: 25px; } }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-people-blue;
          @include breakpoint($lg) {
            background-image: url('../img/icons/icon-people-white.svg'); } } } }
    &_statistics {
      &:before {
        content: '';
        @extend .icon-chart-blue;
        right: 23px;
        @include breakpoint($lg) {
          top: 25px; } }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-chart-blue;
          @include breakpoint($lg) {
            background-image: url('../img/icons/icon-chart-white.svg'); } } } }
    &_centers {
      &:before {
        content: '';
        @extend .icon-card-blue;
        right: 25px;
        @include breakpoint($lg) {
          top: 25px; } }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-card-blue;
          @include breakpoint($lg) {
            background-image: url('../img/icons/icon-card-white.svg'); } } } }
    &_find {
      &:before {
        content: '';
        @extend .icon-search-large-blue;
        right: 25px;
        @include breakpoint($lg) {
          top: 25px; } }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-search-large-blue;
          @include breakpoint($lg) {
            background-image: url('../img/icons/icon-search-large-white.svg'); } } } }
    &_certificates {
      &:before {
        content: '';
        @extend .icon-files-blue;
        right: 25px;
        @include breakpoint($lg) {
          top: 25px; } }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-files-blue;
          @include breakpoint($lg) {
            background-image: url('../img/icons/icon-files-white.svg'); } } } }
    &_appeal {
      &:before {
        content: '';
        @extend .icon-mail-blue;
        right: 25px;
        @include breakpoint($lg) {
          top: 25px; } }
      .services__item-wrapper:hover & {
        &:before {
          @extend .icon-mail-blue;
          @include breakpoint($lg) {
            background-image: url('../img/icons/icon-mail-white.svg'); } } } } }
  &__title {
    font: 400 18px/1.333 $font-default;
    color: $color-black-2;
    @include fluid-type(1100px,1200px,13px,18px);
    .services__item-wrapper:hover & {
      @include breakpoint($lg) {
        color: $color-white-2; } } }
  &__arrow {
    display: none;
    position: absolute;
    left: 20px;
    bottom: 16px;
    width: 21px;
    height: 8px;
    overflow: hidden;
    transition: width 0.4s ease;
    @include breakpoint($lg) {
      display: block; }
    .services__item-wrapper:hover & {
      width: 50px; }
    &:after {
      content: '';
      @extend .icon-arrow-blue-right;
      position: absolute;
      left: -29px;
      top: 0;
      transition: left 0.4s ease; }
    .services__item-wrapper:hover & {
      &:after {
        left: 0;
        @extend .icon-arrow-white-right; } } }
  &__btn {
    width: 100%;
    text-decoration: none;
    display: block;
    height: 48px;
    box-shadow: 0px 0px 10px $color-gray-2;
    background: $color-white;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    font: 400 15px/1.6 $font-default;
    color: $color-blue-5;
    @include link-hover(0.2s,1,0);
    &:hover {
      color: $color-white;
      background: $color-blue-5; }
    &:hover {
      &:after {
        @extend .icon-arrow-white-top; } }
    &:after {
      content: '';
      @extend .icon-arrow-blue-bottom;
      margin: 0 0 0 10px; }
    &_hidden {
      visibility: hidden;
      display: none; } } }
