/*======================================
 *  Selectric v1.13.0
 *====================================== */
.selectric-wrapper {
  position: relative;
  cursor: pointer; }
.selectric-responsive {
  width: 100%; }
.selectric {
  border: none;
  border-radius: 2px;
  background: $color-white;
  position: relative;
  overflow: hidden;
  .label {
    display: block;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    margin: 0 0 0 0;
    color: $color-gray-7;
    user-select: none;
    line-height: normal;
    // padding: 13.5px 10px 13.5px 15px
    padding: 13.5px 10px 13.5px 10px;
    font: 400 13px/1 $font-default;
    @include breakpoint($lg) {
      padding: 16.5px 20px 16.5px 20px;
      background: $color-white; } }
  .button {
    display: block;
    position: absolute;
    right: 0;
    top: 0;
    width: 38px;
    height: 40px;
    background-color: $color-white;
    text-align: center;
    font-size: 0 !important;
    line-height: 0;
    text-indent: -9999px;
    border: none;
    outline: none;
    @include breakpoint($lg) {
      height: 50px; }
    &:after {
      content: " ";
      position: absolute;
      left: auto;
      right: 14px;
      top: calc(50% + 1px);
      transform: translate3d(0,-50%,0);
      @extend .icon-arrow-red-bottom;
      width: 8px;
      height: 5.248px; }
    .selectric-open & {
      &:after {
        @extend .icon-arrow-red-top;
        width: 8px;
        height: 5.248px; } }
    // .selectric-open &
    //   @include breakpoint($lg)
    //     &:after
    //
    @include breakpoint($lg) {
      &:after {
        left: 0;
        right: auto;
        top: 50%;
        width: 12px;
        height: 7.41px; } }
    .selectric-open {
      &:after {
        @extend .icon-arrow-red-top;
        width: 8px;
        height: 5.248px; }
      @include breakpoint($lg) {
        &:after {
          width: 12px;
          height: 7.41px; } } } }
  span {
    font-size: 13px !important;
    @include breakpoint($lg) {
      font-size: 15px !important; } } }
// .selectric-focus .selectric
//   border-color: #aaaaaa
// .selectric-hover .selectric
//   border-color: #c4c4c4
//   .button
//     color: #a2a2a2
//     &:after
//       @extend .icon-arrow-red-bottom
.selectric-open {
  z-index: 9999;
  .selectric {
 }    // @extend .icon-arrow-red-top
  .selectric-items {
    display: block; } }
.selectric-disabled {
  filter: alpha(opacity = 50);
  opacity: 0.5;
  cursor: default;
  user-select: none; }
.selectric-hide-select {
  position: relative;
  overflow: hidden;
  width: 0;
  height: 0;
  select {
    position: absolute;
    left: -100%; }
  &.selectric-is-native {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 10;
    select {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 100%;
      width: 100%;
      border: none;
      z-index: 1;
      box-sizing: border-box;
      opacity: 0; } } }
.selectric-input {
  position: absolute !important;
  top: 0 !important;
  left: 0 !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  margin: 0 !important;
  padding: 0 !important;
  width: 1px !important;
  height: 1px !important;
  outline: none !important;
  border: none !important;
  *font: 0/0 a !important;
  background: none !important; }
.selectric-temp-show {
  position: absolute !important;
  visibility: hidden !important;
  display: block !important; }
/* Items box */
.selectric-items {
  display: none;
  position: absolute;
  top: calc(100% + 8px);
  left: 0;
  background: $color-white;
  border: none;
  z-index: -1;
  box-shadow: 0px 5px 10px rgba($color-blue-14, 0.5);
  .selectric-scroll {
    height: 100%;
    overflow: auto; } }
.selectric-above .selectric-items {
  top: auto;
  bottom: 100%; }
.selectric-items {
  ul {
    list-style: none;
    padding: 7px 0;
    margin: 0;
    min-height: 20px; }
  li {
    min-height: 20px;
    cursor: pointer;
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 15px;
    cursor: pointer;
    margin: 0 0 5px;
    text-decoration: none;
    font: 400 13px/2.066 $font-default;
    color: $color-black-2;
    @include breakpoint($lg) {
      font: 400 15px/2.066 $font-default; }
    &:hover {
      background: $color-blue-15; }
    &:last-child {
      margin: 0 0 0; }
    &.selected {
      background: $color-blue-15;
      color: $color-black-2; }
    &.highlighted {
      background: $color-blue-15;
      color: $color-black-2; }
    &:hover {
      background: $color-blue-15;
      color: $color-black-2; } }
  .disabled {
    filter: alpha(opacity = 50);
    opacity: 0.5;
    cursor: default !important;
    background: none !important;
    color: #666 !important;
    user-select: none; }
  .selectric-group {
    .selectric-group-label {
      font-weight: bold;
      padding-left: 10px;
      cursor: default;
      user-select: none;
      background: none;
      color: #444; }
    &.disabled li {
      filter: alpha(opacity = 100);
      opacity: 1; }
    li {
      padding-left: 25px; } } }
