.footer {
  .col-12 {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    @include breakpoint($lg) {
      display: flex !important;
      flex-direction: row; } }
  &__row {
    &_top {
      background: $color-blue-6;
      padding: 31px 5px 34px 5px;
      @include breakpoint($lg) {
        padding: 41px 0 30px 0; }
      .col-12 {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start; } }
    &_bottom {
      background: $color-blue-10;
      height: auto;
      display: flex;
      align-items: center;
      padding: 10px 5px;
      @include breakpoint($lg) {
        padding: 0 0;
        height: 40px; }
      .col-12 {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        @include breakpoint($lg) {
          display: flex;
          justify-content: flex-start;
          align-items: center; } } } }
  &__nav {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    width: 100%;
    max-width: 734px;
    position: relative;
    @include breakpoint($lg) {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      flex-direction: row; }
    &:before {
      content: '';
      display: none;
      width: 1px;
      height: 133px;
      background: rgba($color-white,0.2);
      position: absolute;
      right: 0;
      top: -4px;
      @include breakpoint($lg) {
        display: block; } } }
  &__item {
    padding: 0 15px 0 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;
    margin: 0 0 11px;
    @include breakpoint($lg) {
      margin: 0 0 0; }
    &_indent {
      padding: 0 0 0 0;
      @include breakpoint($lg) {
        padding: 34px 0 0 0; } }
    &:first-child {
      width: 100%;
      margin: 0 0 32px;
      @include breakpoint($lg) {
        width: 30.7%;
        margin-bottom: 0; } }
    &:nth-child(2) {
      width: 100%;
      @include breakpoint($lg) {
        width: 35%; } }
    &:last-child {
      width: 100%;
      @include breakpoint($lg) {
        width: 33.333%;
        padding-right: 0; } } }
  &__title {
    text-transform: uppercase;
    font: bold 12px/1.25 $font-default;
    color: rgba($color-white, 0.4);
    margin: 0 0 14px;
    letter-spacing: 1px;
    @include breakpoint($lg) {
      margin: 0 0 19px; } }
  &__link {
    text-decoration: none;
    font: 400 13px/1.538 $font-default;
    color: $color-white;
    margin: 0 20px 8px 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    @include link-hover(0.2s,1,0);
    @include breakpoint($lg) {
      font: 400 15px/1.666 $font-default;
      margin: 0 20px 8px 0; }
    &:hover {
      @include link-hover(0.2s,0.8,0); }
    &:last-child {
      margin-bottom: 0;
      @include breakpoint($lg) {
        margin-bottom: 0; } }
    .footer__contacts-item &:last-child {
      margin-right: 0; }
    &_email {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-mail-white-2;
        margin: 2px 7px 0 0;
        width: 16px;
        height: 12.8px;
        @include breakpoint($lg) {
          width: 17px;
          height: 13.6px; } } }
    &_question {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-help-2;
        margin: 0 7px 0 0;
        width: 14px;
        height: 14px;
        @include breakpoint($lg) {
          width: 16px;
          height: 16px; } } }
    &_blank {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      &:before {
        content: '';
        @extend .icon-form;
        margin: 0 7px 0 0; } }
    .footer__nav .footer__link {
      margin-bottom: 10px;
      @include breakpoint($lg) {
        margin-bottom: 0; } } }
  &__contacts {
    padding: 30px 0 0 0;
    margin: 19px 0 0 0;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    border-top: 1px solid rgba($color-white,0.2);
    width: 100%;
    @include breakpoint($lg) {
      display: block;
      width: auto;
      height: auto;
      padding: 0 0 0 50px;
      margin: 0 0 0 0;
      border-top: none; } }
  &__contacts-item {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    margin: 0 0 8px;
    @include breakpoint($lg) {
      display: flex;
      margin: 0 0 0;
      flex-direction: row; }
    &:last-child {
      margin-bottom: 0; } }
  &__copyright {
    &_transparent {
      font: 400 13px/1.538 $font-default;
      color: rgba($color-white, 0.4);
      margin: 0 0 10px;
      @include breakpoint($lg) {
        margin: 0 12px 0 0; } }
    &_white {
      font: 400 13px/1.538 $font-default;
      color: $color-white; } } }
