.map {
  width: 100%;
  position: relative;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  flex-direction: column;
  @include breakpoint($lg) {
    display: block;
    height: 431px;
    padding: 0 0 0; }
  &__container {
    order: 1;
    width: 100%;
    z-index: 1;
    height: 431px;
    @include breakpoint($lg) {
      display: block;
      height: 431px; } }
  &__descr-wrapper {
    order: 2;
    width: 100%;
    position: relative;
    display: flex;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 20px;
    padding-bottom: 20px;
    @include breakpoint($lg) {
      z-index: 2;
      width: 40.082079343365253vw;
      padding-top: 0;
      padding-bottom: 0;
      padding-left: 15px;
      padding-right: 15px;
      position: absolute;
      left: 4.21875vw;
      top: 50%;
      transform: translate3d(0,-50%,0); } }
  &__descr {
    width: 100%;
    background: $color-white;
    display: flex;
    flex-direction: column;
    @include breakpoint($lg) {
      max-width: 586px;
      box-shadow: 0px 10px 20px rgba($color-gray-5, 0.3);
      padding: 40px 40px 33px; } }
  &__title {
    font: bold 20px/1.4 $font-default;
    color: $color-blue-6;
    margin: 0 0 10px;
    @include breakpoint($lg) {
      font: bold 24px/1.333 $font-default;
      margin: 0 0 18px; } }
  &__item {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &:nth-child(2) {
      margin: 0 0 10px;
      @include breakpoint($lg) {
        margin: 0 0 18px; } }
    &:nth-child(3) {
      margin: 0 0 10px;
      @include breakpoint($lg) {
        margin: 0 0 14px; } } }
  &__col {
    &_indent {
      margin: 0 30px 0 0; } }
  &__subtitle {
    font: 400 13px/1.23 $font-default;
    color: $color-gray-6;
    margin: 0 0 5px;
    @include breakpoint($lg) {
      margin: 0 0 4px;
      font: 400 13px/1.538 $font-default; } }
  &__link {
    font: 400 13px/1.538 $font-default;
    color: $color-blue-11;
    text-decoration: none;
    position: relative;
    padding: 0 0 0 21px;
    @include link-hover(0.2s,1,0);
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default; }
    &:hover {
      @include link-hover(0.2s,0.8,0); }
    &:before {
      content: '';
      position: absolute;
      left: 0;
      top: 0; }
    &_address {
      align-items: flex-start;
      &:before {
        @extend .icon-marker;
        top: 4px; } }
    &_email {
      &:before {
        @extend .icon-mail-blue-2;
        top: 4px; } }
    &_phone {
      &:before {
        @extend .icon-phone;
        top: 1.5px; } } }
  &__text {
    font: 400 13px/1.538 $font-default;
    color: $color-gray-6;
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default; } } }
