.help {
  padding: 0 0 0;
  overflow: hidden;
  @include breakpoint($lg) {
    padding: 25px 0; }
  .col-lg-7 {
    order: 1;
    @include breakpoint($lg) {
      order: 0; } }
  .col-lg-4 {
    order: 0;
    padding-left: 0;
    padding-right: 0;
    margin: 0 0 20px;
    @include breakpoint($md) {
      padding-left: 15px;
      padding-right: 15px; }
    @include breakpoint($lg) {
      order: 1;
      margin: 0 0 0; } }
  .offset-1 {
    margin-left: 0;
    @include breakpoint($lg) {
      margin-left: 8.33333%; } }
  &__content {
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    @include breakpoint($lg) {
      display: block;
      &:after {
        content: '';
        width: 1px;
        height: 100%;
        background: $color-gray;
        position: absolute;
        right: -30px;
        top: 0; } } }
  &__title {
    @include h3;
    color: $color-blue;
    margin: 0 0 8px;
    @include breakpoint($lg) {
      margin: 0 0 10px;
      padding: 0 0; } }
  &__text {
    font: 400 13px/1.692 $font-default;
    color: $color-black-2;
    @include fluid-type(320px,1200px,13px,15px);
    @include breakpoint($lg) {
      line-height: 1.6; }
    br {
      display: none;
      @include breakpoint($lg) {
        display: inline-block; } } }
  &__list {
    border-radius: 0 0 2px 2px;
    padding: 17px 0 22px;
    position: relative;
    @include breakpoint($lg) {
      padding: 28px 0 63px; } }
  &__item {
    margin: 0 0 10px;
    background: $color-white;
    box-shadow: 0 0 10px $color-gray-2;
    border-radius: 2px;
    @include breakpoint($lg) {
      margin: 0 0 20px; }
    &:last-child {
      margin-bottom: 0; } }
  &__item-subtitle {
    font: bold 15px/1.333 $font-default;
    color: $color-black-2;
    border-bottom: 1px solid $color-blue-15;
    padding: 15px 15px 15px;
    @include fluid-type(320px,1200px,15px,20px);
    @include breakpoint($lg) {
      line-height: 1.2;
      padding: 25px 30px; } }
  &__item-list {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;
    padding: 10px 15px;
    @include breakpoint($lg) {
      padding: 20px 35px; } }
  &__item-link {
    font: 400 13px/1.538 $font-default;
    color: $color-blue-11;
    text-decoration: none;
    margin: 0 0 16px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    padding: 0 0 0 22px;
    @include fluid-type(320px,1200px,13px,15px);
    &:before {
      content: '';
      @extend .icon-arrow-blue-right-2;
      width: 8.6px;
      height: 14px;
      position: absolute;
      left: 0;
      top: 2px; }
    @include breakpoint($lg) {
      padding: 0 0 0 0;
      margin: 0 0 15px;
      line-height: 1.6;
      &:before {
        width: 4.94px;
        height: 8px;
        margin: 0 10px 0 0;
        position: relative;
        left: auto;
        top: auto; } }
    &:last-child {
      margin-bottom: 0; } }
  .selectric-centers__sidebar_mobile {
    visibility: visible;
    opacity: 1;
    @include breakpoint($lg) {
      visibility: hidden;
      opacity: 0; } }
  .label {
    width: 100%;
    display: flex !important;
    justify-content: flex-start;
    align-items: center;
    .select-default__icon {
      &-0 {
        @extend .icon-dock-black;
        margin: 0 10px 0 0; }
      &-1 {
        @extend .icon-letter-black;
        margin: 0 10px 0 0; }
      &-2 {
        @extend .icon-search-black;
        margin: 0 10px 0 0; }
      &-3 {
        @extend .icon-chart-black;
        margin: 0 10px 0 0; }
      &-4 {
        @extend .icon-people-black;
        margin: 0 10px 0 0; }
      &-5 {
        @extend .icon-card-black;
        margin: 0 10px 0 0; } } } }
