.search {
  display: none;
  margin: 0 auto 0 0;
  width: 100%;
  position: relative;
  justify-content: flex-start;
  align-items: center;
  transform: translate3d(100%,0,0);
  transition: transform 2s linear;
  &_active {
    display: flex;
    transform: translate3d(0,0,0);
    transition: transform 2s linear;
    z-index: 2; }
  &__btn {
    position: absolute;
    left: 18px;
    top: calc(50% + 1px);
    transform: translate3d(0,-50%,0);
    background-color: transparent;
    border: none;
    outline: none;
    width: 22px;
    height: 22px;
    z-index: 0;
    &:after {
      content: '';
      @extend .icon-search;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate3d(-50%,-50%,0); } }
  &__input {
    @include default-text-input(15px, $color-blue-2, $color-blue-2, $color-blue-2, $color-blue-2, none, 0, 21.5px 60px 21.5px 42px, $color-white); }
  &__close {
    position: absolute;
    right: 19px;
    top: 50%;
    transform: translate3d(0,-50%,0);
    text-decoration: none;
    color: transparent;
    @extend .icon-close-circle-gray;
    transition: all 0.5s ease;
    &:hover {
      @extend .icon-close-circle-red; } }
  &__autocomplete {
    background: $color-white;
    box-shadow: 0px 5px 10px rgba($color-blue-14, 0.5);
    list-style: none;
    padding: 7px 0;
    z-index: 3;
    width: 100%;
    height: 324px;
    position: absolute;
    top: 60px;
    left: 0;
    right: 0; }
  &__autocomplete-item {
    width: 100%;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 15px;
    cursor: pointer;
    margin: 0 0 5px;
    text-decoration: none;
    &:hover {
      background: $color-blue-15;
      cursor: pointer; }
    &:last-child {
      margin: 0 0 0; } }
  &__autocomplete-text {
    text-decoration: none;
    &_bold {
      font: bold 13px/2.066 $font-default;
      color: $color-black-2;
      margin: 0 5px 0 0;
      @include breakpoint($lg) {
        font: bold 15px/2.066 $font-default; } }
    &_regular {
      font: 400 13px/2.066 $font-default;
      color: $color-black-2;
      @include breakpoint($lg) {
        font: 400 15px/2.066 $font-default; } }
    &_number {
      font: 400 13px/1 $font-default;
      color: $color-gray;
      margin: 0 0 0 auto;
      @include breakpoint($lg) {
        font: 400 15px/1 $font-default; } } } }
