.qualifications {
  padding: 26px 0 30px;
  background-size: cover;
  background-image: url('../img/bgs/bg-qualifications.png');
  background-repeat: no-repeat;
  background-position: center center;
  position: relative;
  z-index: 2;
  @include breakpoint($lg) {
    padding: 58px 0 53px; }
  &__item {
    .col-lg-7 {
      position: relative; }
    &:first-child {
      .col-lg-3 {
        // border-bottom: 1px rgba($color-white, 0.2) solid
        position: relative;
        padding-bottom: 31px;
        margin-bottom: 26px;
        &:after {
          content: '';
          width: calc(100% - 30px);
          height: 1px;
          background: rgba($color-white, 0.2);
          position: absolute;
          left: 50%;
          bottom: 0;
          transform: translate3d(-50%,0,0);
          @include breakpoint($lg) {
            display: none; } }
        @include breakpoint($lg) {
          border-bottom: 1px rgba($color-white, 0.2) solid;
          padding-bottom: 34px;
          margin-bottom: 36px; } }
      .col-lg-7 {
        @include breakpoint($lg) {
          border-bottom: 1px rgba($color-white, 0.2) solid;
          padding-bottom: 34px;
          margin-bottom: 36px; }
        &:before {
          content: '';
          @extend .icon-qualification;
          position: absolute;
          left: 15px;
          top: 0;
          display: none;
          @include breakpoint($lg) {
            display: block; } } } }
    &:last-child {
      .col-lg-7 {
        &:before {
          content: '';
          @extend .icon-profile;
          position: absolute;
          left: 21px;
          top: -3px;
          display: none;
          @include breakpoint($lg) {
            display: block; } } } }
    .offset-1 {
      margin-left: 0;
      @include breakpoint($lg) {
        margin-left: 8.33333%; } } }
  &__title {
    font: bold 15px/1.6 $font-default;
    color: $color-white;
    margin: 0 0 1px 0;
    padding: 0 15px;
    @include breakpoint($lg) {
      font: bold 18px/1.333 $font-default;
      margin: 0 0 10px 100px;
      padding: 0 0; } }
  &__text {
    font: 400 13px/1.538 $font-default;
    color: $color-white;
    margin: 0 0 10px 0;
    padding: 0 15px;
    @include breakpoint($lg) {
      margin: 0 0 0 100px;
      padding: 0 0; } }
  &__btn {
    border: 1px solid $color-white;
    border-radius: 2px;
    height: 48px;
    font: 400 13px/1.538 $font-default;
    color: $color-white;
    background: transparent;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 10px 15px 0 15px;
    width: calc(100% - 30px);
    position: relative;
    transition: all 0.25s ease;
    @include breakpoint($lg) {
      font: 400 18px/1 $font-default;
      margin: 10px 0 0 0;
      width: 270px; }
    &:after {
      content: '';
      @extend .icon-arrow-white-right-2;
      position: absolute;
      right: 27px;
      top: 50%;
      transform: translate3d(0,-50%,0); }
    &:before {
      content: '';
      position: absolute;
      right: 22px;
      top: 50%;
      transform: translate3d(0,-50%,0);
      width: 20px;
      height: 20px;
      background: linear-gradient(180deg, $color-red 0%, $color-red-2 100%);
      border-radius: 10px;
      opacity: 0;
      transition: all 0.25s ease; }
    &:hover {
      background: $color-white;
      color: $color-blue-6;
      &:before {
        opacity: 1; } }
    .qualifications__item:first-child & {
      padding: 0 0 0 20px;
      @include breakpoint($lg) {
        padding: 0 0 0 25px; } }
    .qualifications__item:last-child & {
      padding: 0 0 0 20px;
      @include breakpoint($lg) {
        padding: 0 0 0 51px; } } } }
