.text-info {
  padding: 15px 0 15px;
  background: $color-gray-3;
  @include breakpoint($lg) {
    padding: 40px 0 40px; }
  .row {
    background: $color-white;
    @include breakpoint($lg) {
      background-color: transparent; }
    &.no-gutters {
      margin-right: 0;
      margin-left: 0;
      &>[class^="col-"],
      &>[class*=" col-"] {
        padding-right: 0;
        padding-left: 0; } } }
  &__item {
    &_white {
      .container {
        .col-lg-6 {
          @include breakpoint($lg) {
            background: $color-white; }
          &:first-child {
            order: 2;
            @include breakpoint($lg) {
              order: 1; } }
          &:last-child {
            order: 1;
            @include breakpoint($lg) {
              order: 2; } } } } }
    &_blue {
      .container {
        .col-lg-6 {
          @include breakpoint($lg) {
            background: linear-gradient(212.28deg, $color-blue-7 11.16%, $color-blue-8 77.95%); }
          &:first-child {
            order: 2;
            @include breakpoint($lg) {
              order: 1; } }
          &:last-child {
            order: 2;
            @include breakpoint($lg) {
              order: 1; } } } } }
    &_red {
      .container {
        .col-lg-6 {
          @include breakpoint($lg) {
            background: linear-gradient(180deg, $color-red 0%, $color-red-2 100%); }
          &:first-child {
            order: 2;
            @include breakpoint($lg) {
              order: 1; } }
          &:last-child {
            order: 1;
            @include breakpoint($lg) {
              order: 2; } } } } } }
  &__title {
    font: bold 20px/1.4 $font-default;
    color: $color-blue-6;
    padding: 15px 15px 10px 15px;
    text-decoration: none;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    @include link-hover(0.2s,1,0);
    cursor: pointer;
    @include breakpoint($lg) {
      padding: 56px 41px 16px 41px;
      font: bold 24px/1.333 $font-default; }
    .text-info__item_white & {
      @include breakpoint($lg) {
        color: $color-blue-6; } }
    .text-info__item_blue & {
      @include breakpoint($lg) {
        color: $color-white; } }
    .text-info__item_red & {
      @include breakpoint($lg) {
        color: $color-white; } }
    @include link-hover(0.2s, 1, 0);
    &:hover {
      @include link-hover(0.2s, 0.8, 0); } }
  &__text {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    padding: 0 15px 15px 15px;
    @include breakpoint($lg) {
      font: 400 15px/1.6 $font-default;
      padding: 0 41px 15px 41px; }
    .text-info__item_white & {
      @include breakpoint($lg) {
        color: $color-black-2; } }
    .text-info__item_blue & {
      @include breakpoint($lg) {
        color: $color-white; } }
    .text-info__item_red & {
      @include breakpoint($lg) {
        color: $color-white; } } }
  &__link {
    width: auto;
    font: bold 12px/1.25 $font-default;
    color: $color-blue-11;
    letter-spacing: 1px;
    text-transform: uppercase;
    text-decoration: none;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 56px;
    padding: 0 15px;
    border-top: 1px solid $color-blue-15;
    @include breakpoint($lg) {
      letter-spacing: normal;
      font: 400 15px/1.6 $font-default;
      text-transform: none;
      padding: 0 0 0 41px;
      margin: 0 0 47px;
      height: auto;
      border-top: none;
      justify-content: flex-start;
      width: 50%; }
    &:after {
      content: '';
      @extend .icon-arrow-white-right-2;
      margin: 0 0 0 9px; }
    &:hover {
      &:after {
        @include link-hover(0.2s,0.8,3px); } }
    .text-info__item_white & {
      color: $color-blue-11;
      opacity: 1;
      @include breakpoint($lg) {
        color: $color-blue-5; }
      &:after {
        content: '';
        @extend .icon-arrow-blue-right-2;
        width: 8px;
        height: 12px;
        @include breakpoint($lg) {
          width: 4.94px;
          height: 8px;
          background-image: url('../img/icons/icon-arrow-blue-right-2.svg'); } } }
    .text-info__item_blue & {
      color: $color-blue-11;
      opacity: 1;
      @include breakpoint($lg) {
        color: $color-white;
        opacity: 0.6; }
      &:after {
        content: '';
        @extend .icon-arrow-blue-right-2;
        @include breakpoint($lg) {
          background-image: url('../img/icons/icon-arrow-white-right-2.svg'); } } }
    .text-info__item_red & {
      color: $color-blue-11;
      opacity: 1;
      @include breakpoint($lg) {
        color: $color-white;
        opacity: 0.6; }
      &:after {
        content: '';
        @extend .icon-arrow-blue-right-2;
        @include breakpoint($lg) {
          background-image: url('../img/icons/icon-arrow-white-right-2.svg'); } } } }
  &__link-img {
    display: block;
    height: 100%;
    text-decoration: none;
    overflow: hidden;
    outline: none; }
  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    display: block;
    transform: scale(1);
    position: relative;
    z-index: 3;
    outline: none;
    transition: transform 0.5s ease;
    &:hover {
      transform: scale(1.01); } }
  .slick-dots {
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    margin: 15px 0 0 0;
    padding: 0;
    li {
      padding: 0;
      margin: 0 5px;
      width: 10px;
      height: 10px;
      border-radius: 5px;
      background: $color-white;
      border: 1px solid rgba($color-blue, 0.1);
      text-indent: -9999px;
      &.slick-active {
        background: $color-blue;
        border-color: $color-blue; } } } }
