.ui {
  padding: 83px 0 231px;
  &__container {
    // width: 100%
    // max-width: 2048px
    width: 2304px;
    padding: 0 125px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start; }
  &__title {
    font: bold 32px/1.25 $font-default;
    color: $color-black-2;
    margin: 0 0 48px; }
  &__col-global {
    &_left {
      width: 100%;
      max-width: 1280px;
      margin: 0 171px 0 0; }
    &_right {
      width: 100%;
      max-width: 600px; } }
  &__row {
    margin: 0 0 66px;
    &_indent-bottom-372 {
      margin: 0 0 372px; }
    &_col-2,
    &_col-3 {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start; }
    &_col-2-justify {
      justify-content: space-between; } }
  &__col {
    .ui__row_col-2 & {
      width: 50%;
      margin: 0 48px 0 0;
      &:last-child {
        margin: 0 0 0 0; } }
    .ui__row_col-3 & {
      width: 33.333%;
      margin: 0 48px 0 0;
      &:last-child {
        margin: 0 0 0 0; } }
    .ui__row_col-2-justify & {
      &:last-child {
        width: 320px; } } }
  &__subtitle {
    font: 400 12px/1.333 $font-default;
    color: $color-gray-7;
    margin: 0 0 20px;
    &_line {
      border-top: 1px solid $color-blue-15;
      padding: 8px 0 0 0;
      margin: 0 0 117px; }
    &_indent-bottom-29 {
      margin: 0 0 29px; } }
  .header-mobile {
    @include breakpoint($lg) {
      display: block; } }
  .ui__col-global_right {
    .footer {
      .col-12 {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column; }
      &__row {
        &_top {
          background: $color-blue-6;
          padding: 31px 5px 34px 5px;
          .col-12 {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start; } }
        &_bottom {
          background: $color-blue-10;
          height: auto;
          display: flex;
          align-items: center;
          padding: 10px 5px;
          .col-12 {
            display: flex;
            justify-content: flex-start;
            align-items: flex-start; } } }
      &__nav {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        width: 100%;
        max-width: 734px;
        position: relative;
        &:before {
          content: '';
          display: none;
          width: 1px;
          height: 133px;
          background: rgba($color-white,0.2);
          position: absolute;
          right: 0;
          top: -4px; } }
      &__item {
        padding: 0 15px 0 0;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        margin: 0 0 11px;
        &_indent {
          padding: 0 0 0 0; }
        &:first-child {
          width: 100%;
          margin: 0 0 32px; }
        &:nth-child(2) {
          width: 100%; }
        &:last-child {
          width: 100%; } }
      &__title {
        text-transform: uppercase;
        font: bold 12px/1.25 $font-default;
        color: rgba($color-white, 0.4);
        margin: 0 0 14px;
        letter-spacing: 1px; }
      &__link {
        text-decoration: none;
        font: 400 13px/1.538 $font-default;
        color: $color-white;
        margin: 0 20px 8px 0;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        @include link-hover(0.2s,1,0);
        &:hover {
          @include link-hover(0.2s,0.8,0); }
        &:last-child {
          margin-bottom: 0; }
        .footer__contacts-item &:last-child {
          margin-right: 0; }
        &_email {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          &:before {
            content: '';
            @extend .icon-mail-white-2;
            margin: 2px 7px 0 0;
            width: 16px;
            height: 12.8px; } }
        &_question {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          &:before {
            content: '';
            @extend .icon-help-2;
            margin: 0 7px 0 0;
            width: 14px;
            height: 14px; } }
        &_blank {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          &:before {
            content: '';
            @extend .icon-form;
            margin: 0 7px 0 0; } }
        .footer__nav .footer__link {
          margin-bottom: 10px; } }
      &__contacts {
        padding: 30px 0 0 0;
        margin: 19px 0 0 0;
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        border-top: 1px solid rgba($color-white,0.2);
        width: 100%; }
      &__contacts-item {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        flex-direction: column;
        margin: 0 0 8px {
          flex-direction: row; }
        &:last-child {
          margin-bottom: 0; } }
      &__copyright {
        &_transparent {
          font: 400 13px/1.538 $font-default;
          color: rgba($color-white, 0.4);
          margin: 0 0 10px; }
        &_white {
          font: 400 13px/1.538 $font-default;
          color: $color-white; } } } }
  .ui__col-global.ui__col-global_left .nav-mobile {
    display: flex;
    position: relative;
    top: auto; }
  .ui__col-global.ui__col-global_left .search-mobile {
    display: flex;
    width: 100%;
    z-index: 2;
    position: relative;
    left: auto;
    top: auto; }
  .ui__col-global.ui__col-global_left .nav-mobile__list.nav-mobile__list_other-sites {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column; }
  .ui__col-global.ui__col-global_left .services__item-wrapper {
    width: 370px; }
  .ui__col-global.ui__col-global_right {
    .services__item {
      width: 290px;
      background: $color-white-2;
      height: 108px;
      margin: 0 0 10px;
      text-decoration: none;
      display: flex;
      justify-content: flex-start;
      align-items: center;
      padding: 20px;
      border-radius: 2px;
      box-shadow: 0px 0px 10px $color-gray-2;
      position: relative;
      transition: all 0.5s ease, background 0.8s linear;
      cursor: pointer;
      transform: scale(1.0);
      &:before {
        position: absolute;
        top: 50%;
        transform: translate3d(0,-50%,0); }
      .services__item-wrapper:active & {
        transform: scale(0.99); }
      &_recommendations {
        &:before {
          content: '';
          @extend .icon-people-blue;
          right: 25px; }
        .services__item-wrapper:hover & {
          &:before {
            @extend .icon-people-blue; } } } }
    .services__title {
      font: 400 13px/1.333 $font-default;
      color: $color-black-2; }
    .services__arrow {
      display: none;
      position: absolute;
      left: 20px;
      bottom: 16px;
      width: 21px;
      height: 8px;
      overflow: hidden;
      transition: width 0.4s ease;
      .services__item-wrapper:hover & {
        width: 50px; }
      &:after {
        content: '';
        @extend .icon-arrow-blue-right;
        position: absolute;
        left: -29px;
        top: 0;
        transition: left 0.4s ease; }
      .services__item-wrapper:hover & {
        &:after {
          left: 0;
          @extend .icon-arrow-white-right; } } }
    .services__btn {
      width: 100%;
      text-decoration: none;
      display: block;
      height: 48px;
      box-shadow: 0px 0px 10px $color-gray-2;
      background: $color-white;
      border-radius: 2px;
      display: flex;
      justify-content: center;
      align-items: center;
      font: 400 15px/1.6 $font-default;
      color: $color-blue-5;
      @include link-hover(0.2s,1,0);
      &:hover {
        color: $color-white;
        background: $color-blue-5; }
      &:hover {
        &:after {
          @extend .icon-arrow-white-top; } }
      &:after {
        content: '';
        @extend .icon-arrow-blue-bottom;
        margin: 0 0 0 10px; }
      &_hidden {
        visibility: hidden;
        display: none; } } }
  .ui__col-global.ui__col-global_right {
    .centers__list-item {
      width: 290px;
      padding: 43px 0 0 0;
      background: $color-white;
      border-radius: 2px;
      box-shadow: 0px 0px 10px $color-blue-14;
      margin: 0 0 10px;
      position: relative;
      &:last-child {
        margin: 0 0 0; } }
    .centers__list-item-title {
      font: bold 15px/1.333 $font-default;
      color: $color-black-2;
      margin: 0 0 10px;
      padding: 0 15px; }
    .centers__list-item-descr {
      font: 400 13px/1.6 $font-default;
      color: $color-black-2;
      margin: 0 0 22px;
      padding: 0 15px; }
    .centers__list-item-contacts {
      display: flex;
      justify-content: flex-start;
      align-items: flex-start;
      padding: 0 15px;
      margin: 0 0 20px;
      flex-direction: column; }
    .centers__list-item-contacts-item {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      font: bold 13px/1.538 $font-default;
      color: $color-blue-11;
      text-decoration: none;
      margin: 0 0 8px;
      position: relative;
      @include link-hover(0.2s,1,0);
      &:hover {
        @include link-hover(0.2s,0.8,0); }
      &:last-child {
        margin: 0 0 0 0; }
      &:before {
        content: '';
        margin: 0 7px 0 0; }
      &_phone {
        &:before {
          @extend .icon-phone;
          width: 16px;
          height: 16px; } }
      &_letter {
        &:before {
          @extend .icon-mail-blue;
          width: 14px;
          height: 11.206px; } }
      &_link {
        &:before {
          @extend .icon-launch; } } }
    .centers__list-item-text {
      padding: 0 15px;
      margin: 0 0 22px; }
    .centers__list-item-text-content {
      font: 400 13px/1.538 $font-default;
      color: $color-black-2;
      margin: 0 0 1px;
      display: flex; }
    .centers__list-item-text-link {
      font: 400 13px/1.538 $font-default;
      color: $color-blue-11;
      text-decoration: none; }
    .centers__list-item-link {
      padding: 0 30px;
      font: bold 12px/1.25 $font-default;
      color: $color-blue-11;
      padding: 0 30px;
      height: 56px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      text-transform: uppercase;
      letter-spacing: 1px;
      text-decoration: none;
      border-top: 1px solid $color-blue-15;
      position: relative;
      @include link-hover(0.2s,1,0);
      &:hover {
        @include link-hover(0.2s,0.8,0); }
      &:after {
        content: '';
        @extend .icon-arrow-blue-right-2;
        width: 7.41px;
        height: 12px; } }
    .centers__list-number {
      font: bold 13px/1.538 $font-default;
      color: $color-red;
      position: absolute;
      left: 15px;
      top: 13px; } }
  &__typo-title {
    border-bottom: 1px solid $color-blue-15;
    &_h1 {
      @include h1;
      padding: 0 0 48px;
      margin: 0 0 24px;
      &.ui__typo-title_mobile {
        display: none;
        font-size: 18px !important;
        line-height: 1.222 !important; } }
    &_h2 {
      padding: 0 0 16px;
      margin: 0 0 24px;
      @include h2;
      &.ui__typo-title_mobile {
        font-size: 18px !important;
        line-height: 1.333 !important; } }
    &_h3 {
      padding: 0 0 24px;
      margin: 0 0 32px;
      @include h3;
      &.ui__typo-title_mobile {
        font-size: 18px !important;
        line-height: 1.333 !important; } }
    &_h4 {
      padding: 0 0 39px;
      margin: 0 0 24px;
      @include h4;
      &.ui__typo-title_mobile {
        font-size: 20px !important;
        line-height: 1.4 !important; } }
    &_h5 {
      padding: 0 0 24px;
      margin: 0 0 32px;
      @include h5;
      &.ui__typo-title_mobile {
        font-size: 13px !important;
        line-height: 1.538 !important; } }
    &_h6 {
      padding: 0 0 42px;
      margin: 0 0 24px;
      @include h6;
      &.ui__typo-title_mobile {
        font-size: 12px !important;
        line-height: 1.25 !important; } } } }
