.prof-qualification {
  padding: 22px 0 0 0;
  overflow: hidden;
  @include breakpoint($lg) {
    padding: 27px 0 40px 0; }
  .container {
    padding-left: 0;
    padding-right: 0;
    @include breakpoint($lg) {
      padding-left: 15px;
      padding-right: 15px; } }
  .offset-1 {
    margin-left: 0;
    @include breakpoint($lg) {
      margin-left: 8.33333%; } }
  &__title {
    @include h3;
    color: $color-blue;
    margin: 0 0 21px;
    padding: 0 15px;
    display: none;
    @include breakpoint($lg) {
      margin: 0 0 30px;
      padding: 0 0; }
    &_active {
      display: block; } }
  &__tabs {}
  &__tabs-titles {
    background: $color-white;
    border-radius: 2px 2px 0 0;
    display: none;
    position: relative;
    z-index: 1;
    @include breakpoint($lg) {
      display: flex;
      justify-content: flex-start;
      align-items: center;
      border-bottom: none;
      border-bottom: 1px solid $color-blue-15; } }
  .selectric-wrapper {
    border-bottom: 2px solid $color-blue-15;
    display: block;
    visibility: visible;
    opacity: 1;
    width: 100%;
    @include breakpoint($lg) {
      visibility: hidden;
      opacity: 0;
      display: none; } }
  .label {
    font: bold 13px/1.538 $font-default !important;
    color: $color-blue-11 !important; }
  .button {
    top: 3px !important; }
  &__tabs-titles-item {
    font: 400 15px/1 $font-default;
    color: $color-black-2;
    padding: 16.5px 20px;
    text-decoration: none;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    @include breakpoint($lg) {
      &:after {
        content: '';
        display: block;
        width: 100%;
        height: 2px;
        background: transparent;
        position: absolute;
        left: 0;
        right: 0;
        bottom: -1px; } }
    &_active {
      @include breakpoint($lg) {
        color: $color-red;
        &:after {
          background: $color-red; } } }
 }    // &_mobile
  &__tabs-content {
    background: $color-white;
    box-shadow: 0 0 10px #ADBED6;
    border-radius: 0 0 2px 2px; }
  &__tabs-content-item {
    display: none;
    padding: 19px 0 22px 0;
    &:last-child {
      padding-bottom: 0;
      @include breakpoint($lg) {
        padding: 0 0 22px; } }
    @include breakpoint($lg) {
      padding: 32px 0 29px 0;
      &:last-child {
        padding: 32px 0 29px 0; } }
    &_active {
      display: block; } }
  &__tabs-content-row {
    border-bottom: 1px solid $color-blue-15;
    margin: 0 0 20px;
    padding: 0 15px 21px;
    position: relative;
    .prof-qualification__tabs-content-item:last-child & {
      padding-left: 0;
      padding-right: 0;
      padding-bottom: 0;
      margin-bottom: 0;
      border-bottom: none; }
    @include breakpoint($lg) {
      padding: 0 30px 30px;
      margin: 0 0 30px;
      .prof-qualification__tabs-content-item:last-child & {
        padding-left: 15px;
        padding-right: 15px; } }
    &:last-child {
      padding-bottom: 0;
      margin-bottom: 0;
      border-bottom: none; }
    @include breakpoint($lg) {
      &:last-child {
        border-bottom: none;
        padding: 0 30px 0;
        margin: 0 0 0; } }
    &_desktop {
      display: none;
      @include breakpoint($lg) {
        display: block; } }
    &_mobile {
      display: block;
      @include breakpoint($lg) {
        display: none; } } }
  &__tabs-content-label {
    font: bold 13px/1.538 $font-default;
    color: $color-red;
    margin: 0 0 11px;
    @include breakpoint($lg) {
      margin: 0 0 15px; } }
  &__tabs-content-title {
    font: bold 15px/1.333 $font-default;
    color: $color-black-2;
    @include fluid-type(320px,1200px,15px,24px);
    margin: 0 0 23px;
    padding: 0 0;
    @include breakpoint($lg) {
      padding: 0 200px 0 0;
      margin: 0 0 30px;
      line-height: 1.333; }
    &:last-child {
      margin-bottom: 0; } }
  &__tabs-content-btn {
    position: relative;
    right: auto;
    top: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    color: $color-white;
    padding: 18.5px 15px;
    border-radius: 4px;
    background: $color-blue-11;
    text-decoration: none;
    font-size: 0;
    line-height: 0;
    &:after {
      content: 'Скачать DOCX';
      display: block;
      font: 400 12px/1 $font-default;
      color: $color-white; }
    &:before {
      content: '';
      @extend .icon-pdf-white;
      margin: 0 5px 0 0; }
    @include breakpoint($lg) {
      justify-content: flex-start;
      font: 400 13px/1 $font-default;
      position: absolute;
      right: 27px;
      top: 22px;
      padding: 9.5px 15px;
      &:after {
        display: none; } } }
  &__tabs-content-subtitle {
    font: 400 13px/1.538 $font-default;
    color: $color-black;
    margin: 0 0 3px;
    @include fluid-type(320px,1200px,13px,18px);
    @include breakpoint($lg) {
      line-height: 1.333;
      color: $color-black-2;
      margin: 0 0 10px; }
    &:last-child {
      margin-bottom: 0; }
    &_small {
      font: 400 13px/1.538 $font-default;
      color: $color-gray-7;
      margin: 0 0 10px;
      @include fluid-type(320px,1200px,13px,15px);
      @include breakpoint($lg) {
        line-height: 1.6;
        margin: 0 0 1px; } } }
  &__tabs-content-text {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    margin: 0 0 12px;
    @include fluid-type(320px,1200px,13px,15px);
    &:last-child {
      margin-bottom: 0; }
    @include breakpoint($lg) {
      line-height: 1.6;
      color: $color-black-2;
      margin: 0 0 15px; } }
  &__tabs-content-text-link {
    color: $color-blue-11;
    text-decoration: none; }
  &__tabs-content-list {
    padding: 0;
    margin: 0 0 0 17px;
    &:last-child {
      margin-bottom: 0; }
    &_indent-bottom-30 {
      margin-bottom: 30px; }
    &_indent-bottom-15 {
      margin-bottom: 15px; } }
  &__tabs-content-list-item {
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    margin: 0 0 12px;
    @include fluid-type(320px,1200px,13px,15px);
    &:last-child {
      margin-bottom: 0; }
    @include breakpoint($lg) {
      line-height: 1.6;
      margin: 0 0 10px; } }
  &__map {
    width: 100%;
    position: relative;
    background: $color-white;
    padding: 0;
    margin: 0 0 0;
    @include breakpoint($lg) {
      display: block;
      margin: 0 0 30px; } }
  &__map-container {
    width: 100%;
    background: $color-white;
    margin: 0;
    padding: 0;
    display: block;
    @include breakpoint($lg) {
      padding: 0;
      margin: 0 0 30px; }
    iframe {
      display: block;
      width: 100%;
      border: none;
      outline: none; } }
  &__map-address {
    padding: 0 15px;
    background: rgba($color-blue-15,0.45);
    border-top: 1px solid $color-blue-15;
    border-bottom: 1px solid $color-blue-15;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    width: 100%;
    height: 49px;
    @include breakpoint($lg) {
      display: none; } }
  &__table {
    padding: 0;
    margin: 0;
    border-collapse: collapse;
    display: none;
    @include breakpoint($lg) {
      display: table; } }
  &__table-thead {}
  &__table-tbody {}
  &__table-tr {}
  &__table-th {
    background: rgba($color-blue-15,0.45);
    padding: 15px 19px 14px;
    font: 400 13px/1.538 $font-default;
    color: $color-black-2;
    border: 1px solid $color-blue-15; }
  &__table-td {
    background: $color-white;
    border: 1px solid $color-blue-15;
    padding: 15px 19px 15px; }
  &__text-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start; }
  &__table-text {
    font: 400 15px/1.6 $font-default;
    color: $color-black-2; }
  // &__table-links
  &__table-link {
    color: $color-blue-11;
    text-decoration: none;
    margin: 0 17px 0 0;
    &:last-child {
      margin: 0 0 0 0; } }
  &__address-item {
    padding: 15px;
    border-bottom: 1px solid $color-blue-15;
    &:last-child {
      border-bottom: none; } }
  &__address-item-line {
    &_top {
      font: 400 13px/1.538 $font-default;
      color: $color-gray-7;
      margin: 0 0 5px; }
    &_middle {
      font: 400 15px/1.466 $font-default;
      color: $color-black-2;
      margin: 0 0 15px; }
    &_bottom {
      display: flex;
      justify-content: space-between;
      align-items: center; } }
  &__address-item-link {
    text-decoration: none;
    font: 400 13px/1.538 $font-default;
    color: $color-blue-11; } }
