.header-mobile {
  position: relative;
  .container {
    height: 60px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding-right: 0; }
  @include breakpoint($lg) {
    display: none; }
  &__logo {
    @extend .icon-logo; }
  &__search {
    width: 54px;
    height: 60px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0 0 0 auto;
    &:after {
      content: '';
      @extend .icon-search;
      width: 18px;
      height: 18px; }
    &_hidden {
      display: none; } }
  &__menu-btn {
    position: relative;
    margin: 0;
    padding: 17px 18px 24px;
    width: 54px;
    height: 60px;
    background: linear-gradient(180deg, #E53935 0%, #BE2421 100%);
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column; }
  &__menu-btn-item {
    content: '';
    width: 18px;
    height: 2px;
    transform: rotate(0deg);
    transition: .5s ease-in-out;
    cursor: pointer;
    text-decoration: none;
    display: block;
    position: relative;
    background: $color-white;
    opacity: 1;
    left: 0;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
    &:nth-child(1) {
      top: 0px; }
    &:nth-child(2) {
      top: 4px; }
    &:nth-child(3) {
      top: 8px; }
    .header-mobile__menu-btn_active & {
      &:nth-child(1) {
        top: 4px;
        transform: rotate(135deg); }
      &:nth-child(2) {
        opacity: 0;
        left: -60px; }
      &:nth-child(3) {
        top: 0;
        transform: rotate(-135deg); } } } }
